import { HttpClient, HttpParams } from '@angular/common/http';
import { Component, inject, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatDialog, MatDialogConfig, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Router } from '@angular/router';
import { dataemployeeModel } from 'src/app/model/employee.model';
import { BrandService } from 'src/app/service/brand.service';
import { EmployeeTypeService } from 'src/app/service/employee-type.service';
import { EmployeeService } from 'src/app/service/employee.service';
import { PositionService } from 'src/app/service/position.service';
import { SiteService } from 'src/app/service/site.service';
import { TypeService } from 'src/app/service/type.service';
import { DataEmployeePopupComponent } from '../data-employee-popup/data-employee-popup.component';
import { EditEmComponent } from '../edit-em/edit-em.component';
import Swal from 'sweetalert2';
import * as moment from 'moment';
import { EmployeeAssetService } from 'src/app/service/employee-asset.service';

@Component({
  selector: 'app-data-employee',
  templateUrl: './data-employee.component.html',
  styleUrls: ['./data-employee.component.css']
})
export class DataEmployeeComponent implements OnInit {

  listData: MatTableDataSource<any>;
  displayedColumns: string[] = ['no', 'employeeCode', 'type', 'prefix', 'firstname', 'lastname', 'nickname', 'position', 'site', 'updateBy','updateDt', 'action'];
  dataSource: MatTableDataSource<any>;
  dataSourceA: MatTableDataSource<any>;
  color = 'gray'
  public roleAccount:boolean;
  public newProfile: any;
  public formDate :FormGroup;
  public search :FormGroup;
  public asset :FormGroup;
  public searchAsset:FormGroup;
  mapEmployee = new Map();
  public detailEmp:any;
  //option filter table//
  public dataposition: any;
  public emType: any;
  public dataSite: any;
  public dataEmployee: any;
  public filter: String;
  public total : any = [];
  renderedData: any;
  public testsite : any = [];

  emListAsset :any = [];
  public test:[]

  @ViewChild('datetime')
    public datetime: DataEmployeeComponent;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(

    private employeeAssetService: EmployeeAssetService,
    private formBuilder: FormBuilder,
    private router: Router,
    public dialog: MatDialog,
    public http: HttpClient,
    private employeeService: EmployeeService,
    private positionService: PositionService,
    private siteService: SiteService,
    private employeeTypeService: EmployeeTypeService
  ) { }

  selectedRowIndex = -1;
  highlight(row: { id: number; }) {
    this.selectedRowIndex = row.id;
  }

  createdForm() {
    this.formDate = this.formBuilder.group({
      startPeriod: new FormControl({ value:'', disabled: false }),
      endPeriod: new FormControl({ value:'', disabled: false }),
    });
    this.search = this.formBuilder.group({
      searchText: new FormControl({ value:'', disabled: false }),
    });
    this.searchAsset = this.formBuilder.group({
      employeeCode: new FormControl({ value:'', disabled: false }),
    });
    this.asset = this.formBuilder.group({
      employeeId: new FormControl({ value:'', disabled: false }),
      assetId:new FormControl({ value:'', disabled: false }),
    });
  }

  ngOnInit() {
    const profile = localStorage.getItem('Profile')
    this.newProfile = JSON.parse(profile)
    if(this.newProfile.role == 'ADMIN'){
      this.roleAccount = true
    }
    this.createdForm();
    //get all table//
    this.refresh()
    //option filter table//
    this.positionService.getPosition().then(result => {
      this.dataposition = result.responseData.data;
    });
    this.employeeTypeService.getEmployType().then(result => {
      this.emType = result.responseData.data;
    });
    this.siteService.getSite().then(result => {
      this.dataSite = result.responseData.data;
    });

  }
  public detailEmployee(row) {
    this.detailEmp = row
    this.router.navigate(['detailEmployee', {data :JSON.stringify(this.detailEmp) }])
  }

  clickdelete(data) {
    Swal.fire({
      title: 'Delete Employee',
      text: "Are you sure you want to delete this Employee?",
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: 'rgb(70, 0, 0)',
      cancelButtonColor: '#696969',
    }).then((result) => {
      if (result.isConfirmed) {
        this.employeeService.deleteEmployee(data).then(() => {
          this.refresh();
        });
        Swal.fire(
          'Deleted!',
          'Your file has been deleted.',
          'success'
        )
      }
    })
  }
  openDialog() {
    const dialogRef = this.dialog.open(DataEmployeePopupComponent, {
      width: '100%',
      data: {
        dataKey: this.dataEmployee
      }
    }).afterClosed().subscribe(result => {
      this.refresh();
    });
  }
  refresh() {
    this.employeeService.getlistEmployee().then(result => {
      this.dataEmployee = result
      this.dataSource = new MatTableDataSource(this.dataEmployee);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
      this.dataSourceA = new MatTableDataSource(this.dataEmployee);
      this.dataSourceA.connect().subscribe(d =>{this.renderedData = d}
      );
    })
  }
  exportExcel(){
    const emListExport: any[] = new Array()
    if(this.renderedData ==''){
      Swal.fire({
        icon: 'error',
        text: 'ไม่พบข้อมูล',
      })
    }else{
      for (let i = 0; i < this.renderedData.length; i++ ){
          this.emListAsset.push("'" + this.renderedData[i].employeeCode + "'");
      }
      this.employeeService.reportEmployee(this.emListAsset).then(result => {
            this.emListAsset = new Array()
            let  listAsset:any[] = new Array()
            listAsset = result
            import('xlsx').then(xlsx => {
              for (let i = 0; i < listAsset.length; i++ ){
                if(listAsset[i].boi==true){
                  listAsset[i].boi ='มี'
                }
                if(listAsset[i].boi==false){
                  listAsset[i].boi ='ไม่มี'
                }
              emListExport.push({
                  EmployeeCode: listAsset[i].employeeCode,
                  EmployeeType: listAsset[i].emType,
                  Prefix: listAsset[i].prefix,
                  Firstname: listAsset[i].firstname,
                  Lastname: listAsset[i].lastname,
                  Nickname: listAsset[i].nickname,
                  Position: listAsset[i].lovType,
                  Site: listAsset[i].siteType,
                  StartingDate: listAsset[i].workStart ? moment(new Date(listAsset[i].workStart)).format('DD/MM/YYYY') : '',
                  ResignationDate:listAsset[i].workEnd ? moment(new Date(listAsset[i].workEnd)).format('DD/MM/YYYY') : '',
                  UpdateBy: listAsset[i].updateBy,
                  UpdateDt: listAsset[i].updateDt ? moment(new Date(listAsset[i].updateDt)).format('DD/MM/YYYY HH:mm') : '',
                  AssetCode: listAsset[i].assetCode,
                  SerialNumber:listAsset[i].serialNumber,
                  Type:listAsset[i].typeName,
                  Brand:listAsset[i].brandName,
                  CPU:listAsset[i].modelType,
                  ReceivedDt:listAsset[i].receivedDt? moment(new Date(listAsset[i].receivedDt)).format('DD/MM/YYYY') : '',
                  ReturnDt:listAsset[i].returnDt? moment(new Date(listAsset[i].returnDt)).format('DD/MM/YYYY') : '',
                  Boi: listAsset[i].boi,
                  Status:listAsset[i].StatusName,
              });
            }
              const worksheet = xlsx.utils.json_to_sheet(emListExport);
              const workbook = { Sheets: { 'data': worksheet }, SheetNames: ['data']  };
              const excelBuffer: any = xlsx.write(workbook, { bookType: 'xlsx', type: 'array' });
              this.saveAsExcelFile(excelBuffer);
            });
          })
    }
  }
  saveAsExcelFile(buffer: any): void {
    import('file-saver').then(FileSaver => {
      const EXCEL_TYPE = 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8';
      const EXCEL_EXTENSION = '.xlsx';
      const data: Blob = new Blob([buffer], {
        type: EXCEL_TYPE
      });
      FileSaver.saveAs(data, 'Employee' + '_export_report_' + new Date().getTime() + EXCEL_EXTENSION);
    });
  }
  onclickListManage() {
    this.router.navigate(['listManage', {}])
  }

  openDialogEdit(data) {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.data = data;
    console.log(data)
    this.dialog.open(EditEmComponent, {
      width: '100%',
      data: {
        dataKey: data,
        dataTable: this.dataEmployee,
      }
    }).afterClosed().subscribe(result => {
      this.refresh();
    });
  }

  public doFilter = (value: string) => {
    this.dataSource.filter = value.trim().toLocaleLowerCase();
    this.dataSourceA.filter = value.trim().toLocaleLowerCase();
  }
  public filterPosition = (value: string) => {
    this.dataSource.filter = value.trim().toLocaleLowerCase();
    this.dataSourceA.filter = value.trim().toLocaleLowerCase();
  }
  public filterSite = (a) => {
    this.testsite = new Array()
    this.employeeService.getlistEmployee().then(result => {
      this.dataEmployee = result
      let b = a._id
      for(let i =0; i<this.dataEmployee.length; i++){
        if(b==result[i].siteid){
          let d = result[i]
          this.testsite.push(d)
        }
        else{
        }
        this.dataSource = new MatTableDataSource(this.testsite);
        this.dataSourceA = new MatTableDataSource(this.testsite);
        this.dataSourceA.connect().subscribe(d =>{this.renderedData = d});

      }
    })
  }
  public filterEmptype = (value: string) => {
    this.dataSource.filter = value.trim().toLocaleLowerCase();
    this.dataSourceA.filter = value.trim().toLocaleLowerCase();
  }

 onclickDate(){
   const startdate : any =  this.formDate.value.startPeriod;
   const Enddate : any = this.formDate.value.endPeriod;
   const data : any  = this.dataSource
   const dataTest : any  = this.dataEmployee
   const total : any = [];
   dataTest.forEach(element => {
    const createdate = new Date(element.updateDt)
    if(moment(createdate).isSameOrAfter(startdate) && moment(createdate).isSameOrBefore(Enddate) ){
      total.push(element)
     }
   });
   console.log( this.formDate.value)
      this.dataSource = new MatTableDataSource(total);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
      this.dataSourceA = new MatTableDataSource(total);
      this.dataSourceA.connect().subscribe(d => this.renderedData = d);
 }

  onclickAsset() {
    this.router.navigate(['data-asset', {}])
  }
  onclickEmployee() {
    this.router.navigate(['employee', {}])
  }
  onclickLogout() {
    this.router.navigate(['login', {}])
  }
  onclickdataadmin() {
    this.router.navigate(['admin', {}])
  }
  onclickchangead() {
    this.router.navigate(['changead', {}])
  }
  onclickManagement() {
    this.router.navigate(['management', {}])
  }
  onclickSite() {
    this.router.navigate(['site', {}])
  }
  onclickBrand() {
    this.router.navigate(['brand', {}])
  }
  onclickfirstname() {
    this.router.navigate(['model', {}])
  }
  onclickType() {
    this.router.navigate(['type', {}])
  }
  clickSearch() {
    this.router.navigate(['changead', {}])
  }
  onclickaccount() {
    this.router.navigate(['list-account', {}])
  }
  onclickEmtype() {
    this.router.navigate(['emtype', {}])
  }
  onclickStatus() {
    this.router.navigate(['status', {}])
  }

  clearFilters(){
    this.refresh()
    this.formDate.patchValue({
      startPeriod: null,
      endPeriod: null,
    })
    this.search.patchValue({
      searchText: null,
    })
    }

  logout(){
    localStorage.removeItem('Authorization');
    this.router.navigate(['login', {}])
  }
}


