import { Injectable } from '@angular/core';
import { HttpParams } from '@angular/common/http';
import { API } from '../url-constants';
import { HttpServices } from './http.service';
import { PositionModel } from '../model/position.model';

@Injectable({
  providedIn: 'root'
})
export class HistoryassetService {
  constructor(
    public http: HttpServices
  ) { }

  postHistoryasset(data: any) {
    const httpParams = new HttpParams();
    const requestOptions = {
      params: httpParams
    };
    return this.http.post(API.CREATEHISTORYASSET, data);
  }
  historyUsedAsset(data: any) {
    const httpParams = new HttpParams();
    const requestOptions = {
      params: httpParams
    };
    return this.http.post(API.HISTORYUSEDASSET, data);
  }


}
