import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { EmployeeTypeService } from 'src/app/service/employee-type.service';
import { EmployeeService } from 'src/app/service/employee.service';
import { PositionService } from 'src/app/service/position.service';
import { SiteService } from 'src/app/service/site.service';
import { PopupAssetComponent } from '../popup-asset/popup-asset.component';
import Swal from 'sweetalert2';
import { EmployeeAssetService } from 'src/app/service/employee-asset.service';
import { StatusService } from 'src/app/service/status.service';
import { AssetService } from 'src/app/service/asset.service';
import { HistoryassetService } from 'src/app/service/historyasset.service';
import { formatDate } from '@angular/common';
import { DatePipe } from '@angular/common';
@Component({
  selector: 'app-edit-em',
  templateUrl: './edit-em.component.html',
  styleUrls: ['./edit-em.component.css']
})
export class EditEmComponent implements OnInit {
  isEnabled: boolean;
  duplicate: boolean;
  receivedMap = new Map();
  returnMap = new Map();
  date = new Date();
  public whereIdChangeStatus = [];
  public whereIdAsset = [];
  public dataEmployee: any;
  public selectAsset: any;
  public selectAssetCheackDup: any;
  public oldAsset = [];
  public deleteAsset = [];
  public addAsset = [];
  public newAsset: any;
  router: any;
  public employee: any;
  public history: FormGroup;
  public putassetStatusUse: FormGroup;
  public formCheckDeleteAsset: FormGroup;
  public formCheckIDasset: FormGroup;
  public formEmployee: FormGroup;
  public formEmpAsset: FormGroup;
  public formCheckEmpcode: FormGroup;
  public formOldEmpAsset: FormGroup;
  public putassetStatus: FormGroup;
  public formPutEmpAsset: FormGroup;
  public otherPrefix: boolean;

  //form employee//
  public employeeCode: string;
  public nickname: string;
  public prefix: string;
  public firstname: string;
  public lastname: string;
  public type: string;
  public position: string;
  public site: string;
  public workStart: string;
  public workEnd: string;
  public status: string;

  //get list data option
  public siteEm: any;
  public positionEm: any;
  public emType: any;

  constructor(
    private historyassetService: HistoryassetService,
    private assetService: AssetService,
    private statusService: StatusService,
    private employeeAssetService: EmployeeAssetService,
    private employeeService: EmployeeService,
    private formBuilder: FormBuilder,
    private siteService: SiteService,
    private employeeTypeService: EmployeeTypeService,
    private positionService: PositionService,
    public dialog: MatDialog,
    public dialogRef: MatDialogRef<EditEmComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
  ) { }

  ngOnInit() {
    this.data.dataKey.employeeCode
    this.formEmployee = this.formBuilder.group({
      employeeCode: new FormControl({ value: '', disabled: false }, [Validators.required]),
      nickname: new FormControl({ value: '', disabled: false }),
      prefix: new FormControl({ value: '', disabled: false }, [Validators.required]),
      firstname: new FormControl({ value: '', disabled: false }, [Validators.required]),
      lastname: new FormControl({ value: '', disabled: false }, [Validators.required]),
      type: new FormControl({ value: '', disabled: false }, [Validators.required]),
      position: new FormControl({ value: '', disabled: false }, [Validators.required]),
      site: new FormControl({ value: '', disabled: false }, [Validators.required]),
      workStart: new FormControl({ value: '', disabled: false }),
      workEnd: new FormControl({ value: '', disabled: false }),
    });
    this.formEmpAsset = this.formBuilder.group({
      employeeId: new FormControl({ value: '', disabled: false }),
      assetId: new FormControl({ value: '', disabled: false }),
      receivedDt: new FormControl({ value: '', disabled: false }, [Validators.required]),
      returnDt: new FormControl({ value: null, disabled: false }),
      status: new FormControl({ value: '', disabled: false }),
    });
    this.formCheckEmpcode = this.formBuilder.group({
      employeeCode: new FormControl({ value: '', disabled: false }, [Validators.required]),
    });

    //foem status
    this.putassetStatus = this.formBuilder.group({
      _id: new FormControl({ value: '', disabled: false }, [Validators.required]),
      state: new FormControl({ value: '', disabled: false }, [Validators.required]),
    });
    this.putassetStatusUse = this.formBuilder.group({
      _id: new FormControl({ value: '', disabled: false }, [Validators.required]),
      state: new FormControl({ value: '', disabled: false }, [Validators.required]),
    });
    this.formCheckDeleteAsset = this.formBuilder.group({
      deleteAsset: new FormControl({ value: '', disabled: false }, [Validators.required]),
    });
    this.formCheckIDasset = this.formBuilder.group({
      IdAsset: new FormControl({ value: '', disabled: false }, [Validators.required]),
    });
    this.history = this.formBuilder.group({
      employeeCode: new FormControl({ value: '', disabled: false }, [Validators.required]),
      assetCode: new FormControl({ value: '', disabled: false }, [Validators.required]),
    });

    //put status empAsset ->N //
    this.formPutEmpAsset = this.formBuilder.group({
      _id: new FormControl({ value: '', disabled: false }),
      employeeId:new FormControl({ value: '', disabled: false }),
      returnDt: new FormControl({ value: '', disabled: false }),
      receivedDt: new FormControl({ value: '', disabled: false }),
      status: new FormControl({ value: '', disabled: false }),
    });

    this.statusService.getStatusFree().then(result => {
      this.status = result[0]._id;
      this.putassetStatus.patchValue({
        state: this.status
      })
    })
    this.statusService.getStatusUsed().then(result => {
      this.status = result[0]._id;
      this.putassetStatusUse.patchValue({
        state: this.status
      })
    })

    //get list data option
    this.siteService.getSite().then(result => {
      this.siteEm = result.responseData.data;
    });
    this.positionService.getPosition().then(result => {
      this.positionEm = result.responseData.data;
    });
    this.employeeTypeService.getEmployType().then(result => {
      this.emType = result.responseData.data;
    });

    //chack prefix
    this.dataEmployee = this.data.dataKey;
    if (this.dataEmployee.prefix == "นาย" ||
      this.dataEmployee.prefix == "นาง" ||
      this.dataEmployee.prefix == "นางสาว"
    ) {
      this.otherPrefix = false
    } else {
      this.otherPrefix = true
    }

    //formEmployee  patchValue
    this.formEmployee.patchValue({
      employeeCode: this.dataEmployee.employeeCode,
      nickname: this.dataEmployee.nickname,
      prefix: this.dataEmployee.prefix,
      firstname: this.dataEmployee.firstname,
      lastname: this.dataEmployee.lastname,
      type: this.dataEmployee.typeid,
      position: this.dataEmployee.positionid,
      site: this.dataEmployee.siteid,
      workStart: this.dataEmployee.workStart,
      workEnd: this.dataEmployee.workEnd,
    })

    this.formCheckEmpcode.patchValue({
      employeeCode: this.data.dataKey.employeeCode
    })

    //formEmpAsset  patchValue
    this.employeeAssetService.listEmpUseAsset(this.formCheckEmpcode.value).then(result => {
      this.selectAsset = result;
      for (let i = 0; i < this.selectAsset.length; i++) {
        this.formEmpAsset.patchValue({
          employeeId: this.dataEmployee.employeeCode,
          assetId: this.selectAsset[i]._id,
          receivedDt: this.selectAsset[i].receivedDt,
        })
        this.receivedMap.set(this.selectAsset[i]._id, formatDate(this.selectAsset[i].receivedDt, 'dd/MM/yyyy', 'en-US'));
      }
      for (let i = 0; i < result.length; i++) {
        this.oldAsset.push(this.selectAsset[i])
      }
      // console.log('this.selectAsset')
      // console.log(this.selectAsset)
      // console.log('this.oldAsset')
      // console.log(this.oldAsset)
      // console.log('selectAssetCheackDup')
      // console.log(this.selectAssetCheackDup)
    });
  }

  onSubmit(){
    try {
      if(this.formEmployee.invalid){
        throw new Error()
      }
      else{
        this.dataEmployee.employeeCode = this.formEmployee.value.employeeCode;
        this.dataEmployee.nickname = this.formEmployee.value.nickname;
        this.dataEmployee.prefix = this.formEmployee.value.prefix;
        this.dataEmployee.nickname = this.formEmployee.value.nickname;
        this.dataEmployee.firstname = this.formEmployee.value.firstname;
        this.dataEmployee.lastname = this.formEmployee.value.lastname;
        this.dataEmployee.type = this.formEmployee.value.type;
        this.dataEmployee.position = this.formEmployee.value.position;
        this.dataEmployee.site = this.formEmployee.value.site;
        this.dataEmployee.workStart = this.formEmployee.value.workStart;
        this.dataEmployee.workEnd = this.formEmployee.value.workEnd;
        this.employeeService.putEmployee(this.dataEmployee)
      }
        if (this.addAsset.length === 0) {
          this.deleteEmpAsset()
          this.returnStatus()
          this.alertSucc()
        }
        else {
          this.deleteEmpAsset()
          this.returnStatus()
          this.addEmployeeAsset()
        }
    }catch (error) {
        Swal.fire({
          icon: 'error',
          text: 'กรอกข้อมูลไม่ถูกต้อง',
        })
    }
  }

  alertSucc() {
    Swal.fire({
      position: 'center',
      icon: 'success',
      title: 'Your work has been saved',
      showConfirmButton: false,
      timer: 1000
    })
    this.dialogRef.close();
  }
  alertErr() {
    Swal.fire({
      icon: 'error',
      text: 'กรุณากรอกวันที่รับเครื่อง',
    })
  }
  onChange(value) {
    if (value === "อื่นๆ") {
      this.formEmployee.patchValue({ prefix: '' })
      this.otherPrefix = true;
    }
    else this.otherPrefix = false;
  }

  openDialog() {
    const dialogRef = this.dialog.open(PopupAssetComponent, {
      height: '80%',
      width: '70%'
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log(result)
      for (let i = 0; i < result.length; i++) {
        this.addAsset.push(result[i])
        this.selectAsset.push(result[i])
      }
      console.log('this.addAsset')
      console.log(this.addAsset)
      console.log('this.selectAsset')
      console.log(this.selectAsset)
      console.log('this.oldAsset')
      console.log(this.oldAsset)
    });
  }
  deleteSelect(i) {
    //delete asset in case add new add asset
    if (this.addAsset != []) {
      for (let index = 0; index < this.addAsset.length; index++) {
        if (this.selectAsset[i].assetCode == this.addAsset[index].assetCode) {
          this.addAsset.splice(index, 1)
        }
      }
    }
    this.deleteAsset.push(this.selectAsset[i])
    this.selectAsset.splice(i, 1)
    console.log(this.deleteAsset)
    console.log(this.selectAsset)
    console.log(this.addAsset)
  }

  addEmployeeAsset() {
    try {
      if (this.selectAsset.length != this.receivedMap.size) {
        throw new Error("");
      }
      for (let i = 0; i < this.addAsset.length; i++) {
        // putassetStatusUse
        this.putassetStatusUse.patchValue({
          _id: this.addAsset[i]._id,
          state: this.putassetStatusUse.value.state,
        })
        this.assetService.putAsset(this.putassetStatusUse.value)
        // addEmployeeAsset
        const dateArray = this.receivedMap.get(this.addAsset[i]._id).split("/");
        var date = new Date(dateArray[2] + "-" + dateArray[1] + "-" + dateArray[0])
        this.formEmpAsset.patchValue({
          employeeId: this.formEmployee.value.employeeCode,
          assetId: this.addAsset[i]._id,
          receivedDt: date,
          status: 'Y'
        })
        this.employeeAssetService.postEmpAsset(this.formEmpAsset.value)
        // addhistoeyasset
        this.history.patchValue({
          employeeCode: this.formEmployee.value.employeeCode,
          assetCode: this.addAsset[i]._id
        })
        this.historyassetService.postHistoryasset(this.history.value)
        // }
      }
      this.alertSucc()
    } catch (error) {
      this.alertErr();

    }
  }

  deleteEmpAsset() {
    for (let i = 0; i < this.deleteAsset.length; i++) {
      this.employeeAssetService.deleteEmpAsset(this.deleteAsset[i])
      //delete employee Asset
      this.formCheckDeleteAsset.patchValue({
        deleteAsset: this.deleteAsset[i].assetId
      })
      this.employeeAssetService.listDeleteAsset(this.formCheckDeleteAsset.value).then(result => {
        this.whereIdAsset = result[0]._id;
        //putassetStatus free
        this.putassetStatus.patchValue({
          _id: this.whereIdAsset,
          state: this.putassetStatus.value.state,
        })
        this.assetService.putAsset(this.putassetStatus.value).then((result) => {
          console.log(result)
        });
      });
    }
  }

  returnStatus() {

    for (let i = 0; i < this.oldAsset.length; i++) {

      if (this.returnMap.get(this.oldAsset[i]._id) != null) {
              //recevived
          const receivedMapArray = this.receivedMap.get(this.oldAsset[i]._id).split("/");
          const dateReceived = new Date(receivedMapArray[2] + "-" + receivedMapArray[1] + "-" + receivedMapArray[0])
            //return
          const returnDtMapArray = this.returnMap.get(this.oldAsset[i]._id).split("/");
          const dateReturn = new Date(returnDtMapArray[2] + "-" + returnDtMapArray[1] + "-" + returnDtMapArray[0])

        this.formPutEmpAsset.patchValue({
          _id: this.oldAsset[i]._id,
          employeeId: this.formEmployee.value.employeeCode,
          receivedDt: dateReceived,
          returnDt: dateReturn,
          status: 'N'
        })
        this.employeeAssetService.putEmpasset(this.formPutEmpAsset.value)
        //edit status Free
        this.formCheckDeleteAsset.patchValue({
          deleteAsset: this.oldAsset[i].assetId
        })
        this.employeeAssetService.listDeleteAsset(this.formCheckDeleteAsset.value).then(result => {
          this.whereIdAsset = result[0]._id;
          this.putassetStatus.patchValue({
            _id: this.whereIdAsset,
            state: this.putassetStatus.value.state,
          })
          this.assetService.putAsset(this.putassetStatus.value).then((result) => {
            console.log(result)
          });
        });
      } else {
         //recevived
         const receivedMapArray = this.receivedMap.get(this.oldAsset[i]._id).split("/");
         const dateReceived = new Date(receivedMapArray[2] + "-" + receivedMapArray[1] + "-" + receivedMapArray[0])

        this.formPutEmpAsset.patchValue({
          _id: this.oldAsset[i]._id,
          employeeId: this.formEmployee.value.employeeCode,
          receivedDt: dateReceived,
          returnDt: null,
          status: 'Y'
        })
        this.employeeAssetService.putEmpasset(this.formPutEmpAsset.value)
      }
    }
  }


  onclickDate(indexSelect) {
    this.receivedMap.set(indexSelect, formatDate(this.formEmpAsset.value.receivedDt, 'dd/MM/yyyy', 'en-US'))
    console.log("this.receivedMap")
    console.log(this.receivedMap)
    console.log(this.receivedMap.get(indexSelect))
  }

  onclickDateReturn(indexSelect) {
    this.returnMap.set(indexSelect, formatDate(this.formEmpAsset.value.returnDt, 'dd/MM/yyyy', 'en-US'))
    console.log("this.returnMap")
    console.log(this.returnMap)
    console.log(this.returnMap.get(indexSelect))
  }
}
