import { Injectable } from '@angular/core';
import { HttpParams } from '@angular/common/http';
import { API } from '../url-constants';
import { HttpServices } from './http.service';

@Injectable({
  providedIn: 'root'
})
export class SaleService {
  constructor(
    public http: HttpServices
  ) { }
  getsale() {
    const httpParams = new HttpParams();
    const requestOptions = {
      params: httpParams
    };
    return this.http.get(API.LISTSALE, requestOptions);
  }
  postsale(data: any) {
    const httpParams = new HttpParams();
    const requestOptions = {
      params: httpParams
    };
    return this.http.post(API.CREATESALE, data);
  }
  deletesale(data: any) {
    //console.log(data)
    const httpParams = new HttpParams();
    const requestOptions = {
      params: httpParams
    };
    return this.http.delete(API.DELETESALE.replace(':_id', data._id));

  }
  putsale(data: any) {
    const httpParams = new HttpParams();
    const requestOptions = {
      params: httpParams
    };
    return this.http.put(API.PUTSALE.replace(':_id', data._id), data);
  }
}
