import { Injectable } from '@angular/core';
import { HttpParams, HttpClient } from '@angular/common/http';
import { API } from '../url-constants';
import { HttpServices } from './http.service';

@Injectable({
  providedIn: 'root'
})
export class AssetService {
  constructor(
    public http: HttpServices
  ) { }
  getAsset() {
    const httpParams = new HttpParams();
    const requestOptions = {
      params: httpParams
    };
    return this.http.get(API.ASSET, requestOptions);
  }
  postdataAsset(data: any) {
    const httpParams = new HttpParams();
    const requestOptions = {
      params: httpParams
    };
    return this.http.post(API.CREATEASSET, data);
  }
getlistasset(){
    const httpParams = new HttpParams();
    const requestOptions = {
      params: httpParams
    };
    return this.http.get(API.LISTASSET, requestOptions);
  }

deleteAsset(data: any) {
  const httpParams = new HttpParams();
    const requestOptions = {
      params: httpParams
    };
    return this.http.delete(API.DELETEASSET.replace(':_id', data._id));
  }
putAsset(data: any) {
    const httpParams = new HttpParams();
    const requestOptions = {
      params: httpParams
    };
    return this.http.put(API.PUTASSET.replace(':_id', data._id), data);
  }
  getlistassetFree(){
    const httpParams = new HttpParams();
    const requestOptions = {
      params: httpParams
    };
    return this.http.get(API.LISTASSETFREE, requestOptions);
  }
  reportAsset(data: any) {
    const httpParams = new HttpParams();
    const requestOptions = {
      params: httpParams
    };
    return this.http.post(API.REPORTASSET, data);
  }
}


