import { Injectable } from '@angular/core';
import { HttpParams, HttpClient } from '@angular/common/http';
import { API } from '../url-constants';
import { HttpServices } from './http.service';
import { SiteComponent } from '../site/components/site/site.component';
import { Observable, throwError } from 'rxjs';

export interface tablesite {
  responseData: SiteComponent[],
    meta: {
      totalresponseData: number;
      responseCount: number;
      responsePerPage: number;
      totalPages: number;
      currentPage: number;
    },
    links: {
      first: string;
      previous: string;
      next: string;
      last: string;
    }
};

@Injectable({
  providedIn: 'root'
})
export class SiteService {

  constructor(
    public http: HttpServices
  ) { }

  getSite() {
    const httpParams = new HttpParams();
    const requestOptions = {
      params: httpParams
    };
    return this.http.get(API.SITE, requestOptions);
  }
  postSite(data: any){
    const httpParams = new HttpParams();
    const requestOptions = {
      params: httpParams
    };
    return this.http.post(API.CREATESITE, data);
  }
  deleteSite(data: any) {
    const httpParams = new HttpParams();
    const requestOptions = {
      params: httpParams
    };
    return this.http.delete(API.DELETESITE.replace(':_id', data._id));
  }
  putSite(data: any) {
    const httpParams = new HttpParams();
    const requestOptions = {
      params: httpParams
    };
    return this.http.put(API.PUTSITE.replace(':_id', data._id), data);

  }

}

