import { Injectable } from '@angular/core';
import { HttpParams } from '@angular/common/http';
import { API } from '../url-constants';
import { HttpServices } from './http.service';

@Injectable({
  providedIn: 'root'
})
export class RepairService {
  constructor(
    public http: HttpServices
  ) { }
  getRepair() {
    const httpParams = new HttpParams();
    const requestOptions = {
      params: httpParams
    };
    return this.http.get(API.LISTREPAIR, requestOptions);
  }
  postRepair(data: any) {
    const httpParams = new HttpParams();
    const requestOptions = {
      params: httpParams
    };
    return this.http.post(API.CREATEREPAIR, data);
  }
  deleteRepair(data: any) {
    //console.log(data)
    const httpParams = new HttpParams();
    const requestOptions = {
      params: httpParams
    };
    return this.http.delete(API.DELETEREPAIR.replace(':_id', data._id));

  }
  putRepair(data: any) {
    const httpParams = new HttpParams();

    const requestOptions = {
      params: httpParams
    };
    return this.http.put(API.PUTREPAIR.replace(':_id', data._id), data);
  }
  searchRepair(data: any) {
    const httpParams = new HttpParams();
    const requestOptions = {
      params: httpParams
    };
    return this.http.post(API.SEARCHREPAIR, data);
  }
}
