import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { assetModel } from 'src/app/model/assetModel.model';
import { TypeModel } from 'src/app/model/type.model';
import { AssetModelService } from 'src/app/service/assetModel.service';
import { TypeService } from 'src/app/service/type.service';
import Swal from 'sweetalert2';
@Component({
  selector: 'app-add-type',
  templateUrl: './add-type.component.html',
  styleUrls: ['./add-type.component.css']
})
export class AddTypeComponent implements OnInit {
  submitted: boolean
  typeTable: any;
  duplicate: boolean;
  createdFormTypeasset: FormGroup;
  constructor(
    private formBuilder: FormBuilder,
    public typeService: TypeService,
    public assetModelService: AssetModelService,
    public dialogRef: MatDialogRef<AddTypeComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
  ) {}
  createdForm() {
    this.createdFormTypeasset = this.formBuilder.group({
      typeName: new FormControl({ value: '', disabled: false }, [Validators.required]),
    });
  }
  ngOnInit(){
    this.createdForm()
    this.typeTable = this.data.dataKey;
  }
  checkDuplicate() {
    for(let i = 0; i < this.typeTable.length; i++){
      if(this.createdFormTypeasset.value.typeName == this.typeTable[i].typeName){
        console.log("Duplicate");
        this.duplicate = true;
        i =this.typeTable.length;
      } else {
        this.duplicate = false;
      }
    }
  }
  onSubmit(){
    this.checkDuplicate();
    if (this.duplicate == true || this.createdFormTypeasset.value.typeName === "" ) {
      this.submitted = false
    } else {
      this.submitted = true
    this.typeService.postType( this.createdFormTypeasset.value).then(() => {
     });
     Swal.fire({
      position: 'center',
      icon: 'success',
      title: 'Your work has been saved',
      showConfirmButton: false,
      timer: 1000
    })
    this.dialogRef.close();
  }
}
onCancel(){
  this.dialogRef.close();
}
}
