import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { ActivatedRoute, Router } from '@angular/router';
import { EmployeeAssetService } from 'src/app/service/employee-asset.service';
import { HistoryassetService } from 'src/app/service/historyasset.service';
import * as moment from 'moment';
@Component({
  selector: 'app-detail-asset',
  templateUrl: './detail-asset.component.html',
  styleUrls: ['./detail-asset.component.css']
})
export class DetailAssetComponent implements OnInit {
  listData: MatTableDataSource<any>;
  displayedColumns: string[] = ['no','employeeCode','prefix','firstname','lastname','type','position','site','Received date','Return date'];
  dataSource: MatTableDataSource<any>;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  public newProfile: any;
  public roleAccount:boolean;
  public findEmpcode: any;
  public findAssetcode: any;
  public formAsset: FormGroup;
  public formEmployee: FormGroup;
  public dataAsset: any;
  public formCheckEmpcode: FormGroup;
  public formCheckAssetcode: FormGroup;
  constructor(
    private historyassetService: HistoryassetService,
    private employeeAssetService: EmployeeAssetService,
    private formBuilder: FormBuilder,
    private router: Router,
    private route: ActivatedRoute,
  ) { }

  ngOnInit() {
    const profile = localStorage.getItem('Profile')
    this.newProfile = JSON.parse(profile)
    if(this.newProfile.role == 'ADMIN'){
      this.roleAccount = true
    }

    this.route.params.subscribe(prams => {
      this.dataAsset = JSON.parse(prams.data);
      console.log('this.dataAsset')
      console.log(this.dataAsset)
    })
    this.formAsset = this.formBuilder.group({
      type: new FormControl({ value: '', disabled: false }),
      brand: new FormControl({ value: '', disabled: false }),
      model: new FormControl({ value: '', disabled: false }),
      color: new FormControl({ value: '', disabled: false }),
      assetCode: new FormControl({ value: '', disabled: false }),
      serialNumber: new FormControl({ value: '', disabled: false }),
      state: new FormControl({ value: '', disabled: false }),
      purchaseDt: new FormControl({ value: '', disabled: false }),
      insuranceDt: new FormControl({ value: '', disabled: false }),
      remark: new FormControl({ value: '', disabled: false }),
    });
    //find employeeCode assetCode
    this.formCheckEmpcode = this.formBuilder.group({
      _id: new FormControl({ value: '', disabled: false }, [Validators.required])
    });
    this.formCheckAssetcode = this.formBuilder.group({
      _id: new FormControl({ value: '', disabled: false }, [Validators.required]),
    });
    //patchValue//
    this.formCheckEmpcode.patchValue({
      _id: this.dataAsset.assetId
    })
    this.formCheckAssetcode.patchValue({
      _id: this.dataAsset.assetId
    })
    debugger
    //find
    this.historyassetService.historyUsedAsset(this.formCheckEmpcode.value).then(result => {
      this.findEmpcode = result;
      this.dataSource = new MatTableDataSource(this.findEmpcode);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    })

    this.employeeAssetService.assetDetail(this.formCheckAssetcode.value).then(result => {
      this.findAssetcode = result;
      console.log(this.findAssetcode)
      for (let i = 0; i < this.findAssetcode.length; i++) {
        this.formAsset.patchValue({
          type: this.findAssetcode[i].typeName,
          brand: this.findAssetcode[i].brandName,
          model: this.findAssetcode[i].modelType,
          color: this.findAssetcode[i].color,
          assetCode: this.findAssetcode[i].assetCode,
          serialNumber: this.findAssetcode[i].serialNumber,
          state: this.findAssetcode[i].StatusName,
          remark: this.findAssetcode[i].remark,
          purchaseDt: this.findAssetcode[i].purchaseDt ? moment(new Date(this.findAssetcode[i].purchaseDt)).format('DD/MM/YYYY') : '',
          insuranceDt: this.findAssetcode[i].insuranceDt ? moment(new Date(this.findAssetcode[i].insuranceDt)).format('DD/MM/YYYY') : '',
        })
        console.log(this.formAsset.value)
      }
    })
  }
  onclickEmployee() {
    this.router.navigate(['employee', {}])
  }
  onclickAsset() {
    this.router.navigate(['data-asset', {}])
  }
  onclickdataadmin() {
    this.router.navigate(['admin', {}])
  }
  onclickaccount() {
    this.router.navigate(['list-account', {}])
  }
  onclickListManage() {
    this.router.navigate(['listManage', {}])
  }
  public doFilter = (value: string) => {
    this.dataSource.filter = value.trim().toLocaleLowerCase();
}
logout() {
  localStorage.removeItem('Authorization');
  this.router.navigate(['login', {}])
}
}
