import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
interface manage {
  value: string;
  viewValue: string;
}
@Component({
  selector: 'app-list-manage',
  templateUrl: './list-manage.component.html',
  styleUrls: ['./list-manage.component.css']
})
export class ListManageComponent implements OnInit {
  public formmanage :FormGroup;

  constructor(
    private router: Router,
    private formBuilder: FormBuilder,
  ) { }

  manages: manage[] = [
    {value: '1', viewValue: 'Position'},
    {value: '2', viewValue: 'Site'},
    {value: '3', viewValue: 'Brand'},
    {value: '4', viewValue: 'CPU'},
    {value: '5', viewValue: 'Asset Type'},
    {value: '6', viewValue: 'Employee Type'},
    {value: '7', viewValue: 'Status'},
  ];

  ngOnInit(){

  }

  onChange(value){

    switch(value){
      case '1' : this.router.navigate(['management',{}]); break;
      case '2' : this.router.navigate(['site',{}]); break;
      case '3' : this.router.navigate(['brand',{}]); break;
      case '4' : this.router.navigate(['model',{}]); break;
      case '5' : this.router.navigate(['type',{}]); break;
      case '6' : this.router.navigate(['emtype',{}]); break;
      case '7' : this.router.navigate(['status',{}]); break;
    }  debugger
  }
  onclickListManage(){
    this.router.navigate(['listManage', {}])
  }
  onclickEmployee() {
    this.router.navigate(['employee',{}])
  }
  onclickAsset() {
    this.router.navigate(['data-asset',{}])
  }
  // onclickManagement() {
  //   this.router.navigate(['management',{}])
  // }
  // onclickSite() {
  //   this.router.navigate(['site',{}])
  // }
  // onclickBrand() {
  //   this.router.navigate(['brand',{}])
  // }
  // onclickModel() {
  //   this.router.navigate(['model',{}])
  // }
  // onclickType() {
  //   this.router.navigate(['type',{}])
  // }
  onclickLogin() {
    this.router.navigate(['login',{}])
  }
  onclickdataadmin() {
    this.router.navigate(['admin',{}])
  }
  onclickaccount() {
    this.router.navigate(['list-account',{}])
  }
  // onclickEmtype() {
  //   this.router.navigate(['emtype',{}])
  // }
  // onclickStatus() {
  //   this.router.navigate(['status',{}])
  // }
  logout() {
    localStorage.removeItem('Authorization');
    this.router.navigate(['login', {}])
  }
}
