import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { ActivatedRoute, Router } from '@angular/router';
import * as moment from 'moment';
import { EmployeeAssetService } from 'src/app/service/employee-asset.service';


@Component({
  selector: 'app-detail-employee',
  templateUrl: './detail-employee.component.html',
  styleUrls: ['./detail-employee.component.css']
})
export class DetailEmployeeComponent implements OnInit {
  listData: MatTableDataSource<any>;
  displayedColumns: string[] = ['no','assetCode','type','brand','Received date','action'];
  dataSource: MatTableDataSource<any>;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  public formEmployee: FormGroup;
  public dataEmp:any;
  public formEmpAsset: FormGroup;
  public formCheckEmpcode: FormGroup;
  public selectAsset: any;
  public detail :any;
  public roleAccount:boolean;
  public newProfile: any;
  panelOpenState = false;
  constructor(
    private employeeAssetService: EmployeeAssetService,
    private route:ActivatedRoute,
    private router: Router,
    private formBuilder: FormBuilder,
  ) {}
  ngOnInit() {
    const profile = localStorage.getItem('Profile')
    this.newProfile = JSON.parse(profile)
    if(this.newProfile.role == 'ADMIN'){
      this.roleAccount = true
    }

    this.route.params.subscribe(prams=>{
     this.dataEmp = JSON.parse(prams.data);
      console.log(this.dataEmp )
    })

    this.formEmployee = this.formBuilder.group({
      employeeCode: new FormControl({ value: '', disabled: false }),
      nickname: new FormControl({ value: '', disabled: false }),
      firstname: new FormControl({ value: '', disabled: false }),
      lastname: new FormControl({ value: '', disabled: false }),
      type: new FormControl({ value: '', disabled: false }),
      position: new FormControl({ value: '', disabled: false }),
      site: new FormControl({ value: '', disabled: false }),
      workStart:new FormControl({ value: '', disabled: false },),
      workEnd:new FormControl({ value: '', disabled: false },),
    });
    this.formEmpAsset = this.formBuilder.group({
      employeeId: new FormControl({ value: '', disabled: false }),
      assetId: new FormControl({ value: '', disabled: false }),
    });
    this.formCheckEmpcode = this.formBuilder.group({
      employeeCode: new FormControl({ value: '', disabled: false }, [Validators.required]),
    });

    this.formEmployee.patchValue({
      employeeCode: this.dataEmp.employeeCode,
      nickname: this.dataEmp.nickname,
      firstname: this.dataEmp.firstname,
      lastname: this.dataEmp.lastname,
      type: this.dataEmp.emType,
      position: this.dataEmp.lovType,
      site: this.dataEmp.siteType,
      workStart :this.dataEmp.workStart? moment(new Date(this.dataEmp.workStart)).format('DD/MM/YYYY') : '',
      workEnd :this.dataEmp.workEnd? moment(new Date(this.dataEmp.workEnd)).format('DD/MM/YYYY') : '',
    })
    this.formCheckEmpcode.patchValue({
      employeeCode: this.dataEmp.employeeCode
    })

    this.employeeAssetService.listEmpUseAsset(this.formCheckEmpcode.value).then(result => {
      this.selectAsset = result;
      for (let i = 0; i < this.selectAsset.length; i++) {
        this.dataSource = new MatTableDataSource(this.selectAsset);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
      }
    })
    }

  detailAsset(list) {
      this.detail = list
      this.router.navigate(['detailAsset', {data :JSON.stringify(this.detail) }])
    }

  onChange(value: any){
    switch(value){
      case '1' : this.router.navigate(['management',{}]); break;
      case '2' : this.router.navigate(['site',{}]); break;
      case '3' : this.router.navigate(['brand',{}]); break;
      case '4' : this.router.navigate(['model',{}]); break;
      case '5' : this.router.navigate(['type',{}]); break;
      case '6' : this.router.navigate(['emtype',{}]); break;
      case '7' : this.router.navigate(['status',{}]); break;
    }
  }
  onclickListManage(){
    this.router.navigate(['listManage', {}])
  }
  onclickEmployee() {
    this.router.navigate(['employee',{}])
  }
  onclickAsset() {
    this.router.navigate(['data-asset',{}])
  }
  onclickdataadmin() {
    this.router.navigate(['admin',{}])
  }
  onclickaccount() {
    this.router.navigate(['list-account',{}])
  }
  logout() {
    localStorage.removeItem('Authorization');
    this.router.navigate(['login', {}])
  }

}
