import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { BrandModel } from 'src/app/model/brand.model';
import { BrandService } from 'src/app/service/brand.service';
import { TypeService } from 'src/app/service/type.service';
import Swal from 'sweetalert2';
@Component({
  selector: 'app-edit-brand',
  templateUrl: './edit-brand.component.html',
  styleUrls: ['./edit-brand.component.css']
})
export class EditBrandComponent implements OnInit {

  submitted: boolean
  dataBrand: any;
  public brand: any;
  public brandName: string;
  public brandType: string;
  duplicate: boolean;
  public OldebrandName:any
  public OldebrandType:any

  brand_chackDup: any;
  public type: any;
  createdFormBrand: FormGroup;
  constructor(
    private typeService: TypeService,
    private formBuilder: FormBuilder,
    private brandService: BrandService,
    public dialogRef: MatDialogRef<EditBrandComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
  ) {}
  ngOnInit() {
    this.createdFormBrand = this.formBuilder.group({
      brandName: new FormControl({ value: '', disabled: false }, [Validators.required]),
      brandType: new FormControl({ value: '', disabled: false }, [Validators.required]),
    });

    this.typeService.getType().then((result) => {
      this.type = result.responseData.data;
    });
    this.brand = this.data.dataKey;
    this.brandName = this.brand.brandName
    this.brandType = this.brand.brandType
    this.brand_chackDup = this.data.dataTable
    this.createdFormBrand.patchValue({
      brandName: this.brandName,
      brandType: this.brand.typeid
    })
    this.OldebrandName = this.brandName
    this.OldebrandType = this.brand.typeid
    }

  onSubmit() {
    if(this.OldebrandName ==this.createdFormBrand.value.brandName && this.OldebrandType == this.createdFormBrand.value.brandType){
      this.dialogRef.close();
    }else{
      this.checkDuplicate();
          if (this.duplicate == true || this.createdFormBrand.value.brandName === '') {
            this.submitted = false
          } else {
            this.submitted = true
            this.brand.brandName = this.createdFormBrand.value.brandName;
            this.brand.brandType = this.createdFormBrand.value.brandType;
            this.brandService.putBrand(this.brand).then(() => {
            });
            Swal.fire({
              position: 'center',
              icon: 'success',
              title: 'Your work has been saved',
              showConfirmButton: false,
              timer: 1000
            })
            this.dialogRef.close();
          }
    }
  }
  checkDuplicate() {
    for (let i = 0; i < this.brand_chackDup.length; i++) {
      if (this.createdFormBrand.value.brandName == this.brand_chackDup[i].brandName
        && this.createdFormBrand.value.brandType == this.brand_chackDup[i].brandType ) {
        console.log("Duplicate");
        this.duplicate = true;
        i = this.brand_chackDup.length;
      } else {
        this.duplicate = false;
      }
    }
  }
  onCancel() {
    this.dialogRef.close();
  }

}

