import { Component, inject, OnInit, ViewChild } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { MatDialog, MatDialogConfig, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { PositionModel } from 'src/app/model/position.model';
import { PositionService } from 'src/app/service/position.service';
import { EditPoComponent } from '../edit-po/edit-po.component';
import { ManagementPopupComponent } from '../position-popup/management-popup.component';
import Swal from 'sweetalert2';
import { MatTableDataSource } from '@angular/material/table';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
interface manage {
  value: string;
  viewValue: string;
}
@Component({
  selector: 'app-management',
  templateUrl: './management.component.html',
  styleUrls: ['./management.component.css']
})
export class ManagementComponent implements OnInit {

  listData: MatTableDataSource<any>;
  displayedColumns: string[] = ['no', 'position','updateDt','updateBy','action'];
  dataSource: MatTableDataSource<any>;
  color = 'gray'

  public dataPosition: any;
  positionID = new PositionModel();

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private router: Router,
    public dialog: MatDialog,
    private positionService: PositionService,
  ) { }

  selectedRowIndex = -1;
  highlight(row: { id: number; }){
    this.selectedRowIndex = row.id;
}

manages: manage[] = [
  {value: '1', viewValue: 'Position'},
  {value: '2', viewValue: 'Site'},
  {value: '3', viewValue: 'Brand'},
  {value: '4', viewValue: 'CPU'},
  {value: '5', viewValue: 'Asset Type'},
  {value: '6', viewValue: 'Employee Type'},
  {value: '7', viewValue: 'Status'},
];

  ngOnInit() {
    this.positionService.getPosition().then(result => {
      this.dataPosition = result.responseData.data;
      this.dataSource = new MatTableDataSource(this.dataPosition);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    })
  }

  openDialog() {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.data = this.dataPosition;
    this.dialog.open(ManagementPopupComponent, {
      data: {
        dataKey: this.dataPosition
      }
    }).afterClosed().subscribe(result => {
      this.refresh();
    });
  }
  openDialogEditpo(data) {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.data = data;
    console.log(data)
    this.dialog.open(EditPoComponent, {
      data: {
        dataKey: data,
        dataTable: this.dataPosition
      }
    }).afterClosed().subscribe(result => {
      this.refresh();
    });
  }
  onChange(value){
    switch(value){
      case '1' : this.router.navigate(['management',{}]); break;
      case '2' : this.router.navigate(['site',{}]); break;
      case '3' : this.router.navigate(['brand',{}]); break;
      case '4' : this.router.navigate(['model',{}]); break;
      case '5' : this.router.navigate(['type',{}]); break;
      case '6' : this.router.navigate(['emtype',{}]); break;
      case '7' : this.router.navigate(['status',{}]); break;
    }
  }
  refresh() {
    this.positionService.getPosition().then(result => {
      this.dataPosition = result.responseData.data;
      this.dataSource = new MatTableDataSource(this.dataPosition);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    })
  }

  public doFilter = (value: string) => {
    this.dataSource.filter = value.trim().toLocaleLowerCase();
  }
  clickdelete(data) {
    Swal.fire({
      title: 'Delete Position',
      text: "Are you sure you want to delete this position?",
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: 'rgb(70, 0, 0)',
      cancelButtonColor: '#696969',
    }).then((result) => {
      if (result.isConfirmed) {
        this.positionService.deletePosition(data).then(() => {
        });
        this.refresh();
        Swal.fire(
          'Deleted!',
          'Your file has been deleted.',
          'success'
        )
      }
    })
  }
  onclickListManage(){
    this.router.navigate(['listManage', {}])
  }
  onclickEmployee() {
    this.router.navigate(['employee', {}])
  }
  onclickAsset() {
    this.router.navigate(['data-asset', {}])
  }
  onclickdataadmin() {
    this.router.navigate(['admin', {}])
  }
  onclickaccount() {
    this.router.navigate(['list-account',{}])
  }
  logout() {
    localStorage.removeItem('Authorization');
    this.router.navigate(['login', {}])
  }
}
