import { Component, inject, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { MatDialog , MatDialogConfig, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import { Router } from '@angular/router';
import { SiteService } from 'src/app/service/site.service';
import { EditSiteComponent } from '../edit-site/edit-site.component';
import {SitePopupComponent } from '../site-popup/site-popup.component';
import {AfterViewInit, ViewChild} from '@angular/core';
import {MatPaginator} from '@angular/material/paginator';
import {MatTableDataSource} from '@angular/material/table';
import { MatSort } from '@angular/material/sort';
import Swal from 'sweetalert2';
interface manage {
  value: string;
  viewValue: string;
}

@Component({
  selector: 'app-site',
  templateUrl: './site.component.html',
  styleUrls: ['./site.component.css']
})

export class SiteComponent implements OnInit {
  listData: MatTableDataSource<any>;
  displayedColumns: string[] = ['no', 'site','updateDt','updateBy','action'];
  dataSource: MatTableDataSource<any>;

  public dataSite : any;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private router: Router,
    public dialog : MatDialog,
    private siteService: SiteService,
  ){}

  selectedRowIndex = -1;
    highlight(row: { id: number; }){
    this.selectedRowIndex = row.id;
}

manages: manage[] = [
  {value: '1', viewValue: 'Position'},
  {value: '2', viewValue: 'Site'},
  {value: '3', viewValue: 'Brand'},
  {value: '4', viewValue: 'CPU'},
  {value: '5', viewValue: 'Asset Type'},
  {value: '6', viewValue: 'Employee Type'},
  {value: '7', viewValue: 'Status'},
];

  ngOnInit(): void {
    this.siteService.getSite().then(result => {
      this.dataSite = result.responseData.data;
      this.dataSource = new MatTableDataSource(this.dataSite);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    })
  }
  refresh() {
    this.siteService.getSite().then(result => {
      this.dataSite = result.responseData.data;
      this.dataSource = new MatTableDataSource(this.dataSite);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    })
  }

  openDialog() {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.data = this.dataSite;
    this.dialog.open(SitePopupComponent, {
      data: {
        dataKey: this.dataSite
      }
    }).afterClosed().subscribe(result => {
      this.refresh();
    });
  }

  openDialogEditsite(data) {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.data = data;
    const dialogRef = this.dialog.open(EditSiteComponent, {
      data: {
        dataKey: data,
        dataTable: this.dataSite
      }
    }).afterClosed().subscribe(result => {
      this.refresh();
    });
  }

  public doFilter = (value: string) => {
    this.dataSource.filter = value.trim().toLocaleLowerCase();
  }
  clickdelete(data) {
    Swal.fire({
      title: 'Delete Site',
      text: "Are you sure you want to delete this Site?",
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: 'rgb(70, 0, 0)',
      cancelButtonColor: '#696969',
    }).then((result) => {
      if (result.isConfirmed) {
        this.siteService.deleteSite(data).then(() => {
        });
        this.refresh();
        Swal.fire(
          'Deleted!',
          'Your file has been deleted.',
          'success'
        )
      }
    })
  }
  onChange(value){
    switch(value){
      case '1' : this.router.navigate(['management',{}]); break;
      case '2' : this.router.navigate(['site',{}]); break;
      case '3' : this.router.navigate(['brand',{}]); break;
      case '4' : this.router.navigate(['model',{}]); break;
      case '5' : this.router.navigate(['type',{}]); break;
      case '6' : this.router.navigate(['emtype',{}]); break;
      case '7' : this.router.navigate(['status',{}]); break;
    }
  }
  onclickListManage(){
    this.router.navigate(['listManage', {}])
  }
  onclickEmployee() {
    this.router.navigate(['employee',{}])
  }
  onclickAsset() {
    this.router.navigate(['data-asset',{}])
  }
  onclickdataadmin() {
    this.router.navigate(['admin',{}])
  }
  onclickaccount() {
    this.router.navigate(['list-account',{}])
  }
  logout() {
    localStorage.removeItem('Authorization');
    this.router.navigate(['login', {}])
  }
}

