import { Component, OnInit , Inject} from '@angular/core';
import { PositionService } from 'src/app/service/position.service';
import {MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import Swal from 'sweetalert2';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-edit-po',
  templateUrl: './edit-po.component.html',
  styleUrls: ['./edit-po.component.css']
})
export class EditPoComponent implements OnInit {
  submitted : boolean
  public position: any;
  public lovType: string;
  duplicate: boolean;
  position_chackDup: any;
  createdFormPosition: FormGroup;
  public olddata:any
  constructor(
    private formBuilder: FormBuilder,
    private positionService: PositionService,
    public dialogRef: MatDialogRef<EditPoComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
  ) { }
  ngOnInit() {
    this.position = this.data.dataKey;
    this.lovType = this.position.lovType
    this.position_chackDup = this.data.dataTable
    this.createdFormPosition = this.formBuilder.group({
        lovType: new FormControl({ value: '' ,disabled: false }, [Validators.required]),
    });
    this.createdFormPosition.patchValue({
      lovType:this.lovType,
    })
    this.olddata = this.lovType
  }
  onSubmit() {
    if(this.olddata ==this.createdFormPosition.value.lovType){
      this.dialogRef.close();
    }else{
      this.checkDuplicate();
      if (this.duplicate == true || this.createdFormPosition.value.lovType === ''  ) {
        this.submitted = false
      } else {
        this.submitted = true
        // this.createdFormPosition.value.position = this.createdFormPosition.value.lovType;
        this.position.lovType = this.createdFormPosition.value.lovType;
        this.positionService.putPosition(this.position).then(() => {
       });
       Swal.fire({
        position: 'center',
        icon: 'success',
        title: 'Your work has been saved',
        showConfirmButton: false,
        timer: 1500
      })
      this.dialogRef.close();
      }

    }
}
checkDuplicate() {
  console.log("this.lovType =" +this.lovType)
  for(let i = 0; i < this.position_chackDup.length; i++){
    if(this.createdFormPosition.value.lovType == this.position_chackDup[i].lovType){
      console.log("Duplicate");
      this.duplicate = true;
      i =this.position_chackDup.length;
    } else {
      this.duplicate = false;
    }
  }
}
onCancel(){
  this.dialogRef.close();
}
}


