import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { StatusModel } from 'src/app/model/status.model';
import { StatusService } from 'src/app/service/status.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-add-status',
  templateUrl: './add-status.component.html',
  styleUrls: ['./add-status.component.css']
})
export class AddStatusComponent implements OnInit {
  createdFormStatus: FormGroup;
  submitted: boolean
  statusTable: any;
  duplicate: boolean;
  constructor(
    private formBuilder: FormBuilder,
    public statusService: StatusService,
    private router: Router,
    public dialogRef: MatDialogRef<AddStatusComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
  ) {}

  createdForm() {
    this.createdFormStatus = this.formBuilder.group({
      StatusName: new FormControl({ value: '', disabled: false }, [Validators.required]),
    });
  }
  ngOnInit() {
    this.createdForm()
    this.statusTable = this.data.dataKey;
  }
  checkDuplicate() {
    for(let i = 0; i < this.statusTable.length; i++){
      if(this.createdFormStatus.value.StatusName == this.statusTable[i].StatusName){
        console.log("Duplicate");
        this.duplicate = true;
        i =this.statusTable.length;
      } else {
        this.duplicate = false;
      }
    }
  }
  onSubmit(){
    this.checkDuplicate();
    if (this.duplicate == true || this.createdFormStatus.value.StatusName === "" ) {
      this.submitted = false
    } else {
      this.submitted = true
    this.statusService.postStatus(this.createdFormStatus.value).then(() => {
    });
    Swal.fire({
      position: 'center',
      icon: 'success',
      title: 'Your work has been saved',
      showConfirmButton: false,
      timer: 1000
    })
    this.dialogRef.close();
  }
}
onCancel(){
  this.dialogRef.close();
}
}
