import { Component, OnInit, ViewChild } from '@angular/core';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Router } from '@angular/router';
import { StatusService } from 'src/app/service/status.service';
import Swal from 'sweetalert2';
import { AddStatusComponent } from '../add-status/add-status.component';
import { EditStatusComponent } from '../edit-status/edit-status.component';
interface manage {
  value: string;
  viewValue: string;
}

@Component({
  selector: 'app-status',
  templateUrl: './status.component.html',
  styleUrls: ['./status.component.css']
})

export class StatusComponent implements OnInit {

  listData: MatTableDataSource<any>;
  displayedColumns: string[] = ['no', 'status','updateDt','updateBy','action'];
  dataSource: MatTableDataSource<any>;
  public dataStatus: any;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private router: Router,
    public dialog : MatDialog,
    private statusService: StatusService
  ){}

  selectedRowIndex = -1;
  highlight(row: { id: number; }){
    this.selectedRowIndex = row.id;
}
manages: manage[] = [
  {value: '1', viewValue: 'Position'},
  {value: '2', viewValue: 'Site'},
  {value: '3', viewValue: 'Brand'},
  {value: '4', viewValue: 'CPU'},
  {value: '5', viewValue: 'Asset Type'},
  {value: '6', viewValue: 'Employee Type'},
  {value: '7', viewValue: 'Status'},
];

  ngOnInit() {
    this.statusService.getStatus().then(result => {
      this.dataStatus = result.responseData.data;
      this.dataSource = new MatTableDataSource(this.dataStatus);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    })
  }
  openDialog() {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.data = this.dataStatus;
    this.dialog.open(AddStatusComponent, {
      data: {
        dataKey: this.dataStatus
      }
    }).afterClosed().subscribe(result => {
      this.refresh();
    });
  }
  refresh() {
    this.statusService.getStatus().then(result => {
      this.dataStatus = result.responseData.data;
      this.dataSource = new MatTableDataSource(this.dataStatus);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    })
  }

  public doFilter = (value: string) => {
    this.dataSource.filter = value.trim().toLocaleLowerCase();
  }
  openDialogStatus(data) {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.data = data;
    this.dialog.open(EditStatusComponent, {
      data: {
        dataKey: data,
        dataTable: this.dataStatus
      }
    }).afterClosed().subscribe(result => {
      this.refresh();
    });
  }
  clickdelete(data) {
    Swal.fire({
      title: 'Delete status',
      text: "Are you sure you want to delete this status?",
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: 'rgb(70, 0, 0)',
      cancelButtonColor: '#696969',
    }).then((result) => {
      if (result.isConfirmed) {
        this.statusService.deleteStatus(data).then(() => {
        });
        this.refresh();
        Swal.fire(
          'Deleted!',
          'Your file has been deleted.',
          'success'
        )
      }
    })
  }
  onChange(value){
    switch(value){
      case '1' : this.router.navigate(['management',{}]); break;
      case '2' : this.router.navigate(['site',{}]); break;
      case '3' : this.router.navigate(['brand',{}]); break;
      case '4' : this.router.navigate(['model',{}]); break;
      case '5' : this.router.navigate(['type',{}]); break;
      case '6' : this.router.navigate(['emtype',{}]); break;
      case '7' : this.router.navigate(['status',{}]); break;
    }
  }
  onclickListManage(){
    this.router.navigate(['listManage', {}])
  }
  onclickEmployee() {
    this.router.navigate(['employee',{}])
  }
  onclickAsset() {
    this.router.navigate(['data-asset',{}])
  }
  onclickdataadmin() {
    this.router.navigate(['admin',{}])
  }
  onclickaccount() {
    this.router.navigate(['list-account',{}])
  }
  logout() {
    localStorage.removeItem('Authorization');
    this.router.navigate(['login', {}])
  }
  }

