import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { UserAccountService } from 'src/app/service/userAccount.service';
import { Router } from '@angular/router';
import { PositionService } from 'src/app/service/position.service';
import { PositionModel } from 'src/app/model/position.model'
import Swal from 'sweetalert2';

@Component({
  selector: 'app-management-popup',
  templateUrl: './management-popup.component.html',
  styleUrls: ['./management-popup.component.css']
})

export class ManagementPopupComponent implements OnInit {
  submitted: boolean
 // position = new PositionModel();
  positionTable: any;
  duplicate: boolean;
  createdFormPosition: FormGroup;

  constructor(
    private formBuilder: FormBuilder,
    public positionService: PositionService,
    public dialogRef: MatDialogRef<ManagementPopupComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) {}

  createdForm() {
    this.createdFormPosition = this.formBuilder.group({
      lovType: new FormControl({ value: '', disabled: false }, [Validators.required]),
    });
  }

  ngOnInit() {
    this.createdForm();
    // console.log("this.createdFormPosition.value.lovType ="+this.createdFormPosition.value.lovType)
    this.positionTable = this.data.dataKey;
    // console.log("this.positionTable ="+this.positionTable)
  }
  checkDuplicate() {
    for(let i = 0; i < this.positionTable.length; i++){
      if(this.createdFormPosition.value.lovType == this.positionTable[i].lovType){
        console.log("Duplicate");
        this.duplicate = true;
        i = this.positionTable.length;
      } else {
        this.duplicate = false;
      }
      console.log(this.duplicate)
    }
  }

  onSubmit() {
    this.checkDuplicate();
    if (this.duplicate == true || this.createdFormPosition.value.lovType === "" ) {
      this.submitted = false
    } else {
      this.submitted = true
      console.log(this.createdFormPosition.value);
      this.positionService.postPosition(this.createdFormPosition.value).then(() => {
      });
      Swal.fire({
        position: 'center',
        icon: 'success',
        title: 'Your work has been saved',
        showConfirmButton: false,
        timer: 1500
      })
      this.dialogRef.close();
    }
  }
  onCancel(){
    this.dialogRef.close();
  }
}

