import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { SiteModel } from 'src/app/model/site.model';
import { SiteService } from 'src/app/service/site.service';
import Swal from 'sweetalert2';
@Component({
  selector: 'app-site-popup',
  templateUrl: './site-popup.component.html',
  styleUrls: ['./site-popup.component.css']
})
export class SitePopupComponent implements OnInit {
  submitted: boolean
  duplicate: boolean;
  siteTable: any;
  createdFormSite: FormGroup;
  constructor(
    private formBuilder: FormBuilder,
    public siteService: SiteService,
    private router: Router,
    public dialogRef: MatDialogRef<SitePopupComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
  ) {}
  createdForm() {
    this.createdFormSite = this.formBuilder.group({
      siteType: new FormControl({ value: '', disabled: false }, [Validators.required]),
    });
  }

  ngOnInit(){
    this.createdForm()
    this.siteTable = this.data.dataKey;
  }
  checkDuplicate() {
    for(let i = 0; i < this.siteTable.length; i++){
      if(this.createdFormSite.value.siteType == this.siteTable[i].siteType){
        console.log("Duplicate");
        this.duplicate = true;
        i =this.siteTable.length;
      } else {
        this.duplicate = false;
      }
    }
  }

  onSubmit(){
    this.checkDuplicate();
    if (this.duplicate == true || this.createdFormSite.value.siteType === "" ) {
      this.submitted = false
    } else {
      this.submitted = true
    this.siteService.postSite(this.createdFormSite.value).then(() => {
    });
    Swal.fire({
      position: 'center',
      icon: 'success',
      title: 'Your work has been saved',
      showConfirmButton: false,
      timer: 1000
    })
    this.dialogRef.close();
  }
}
onCancel(){
  this.dialogRef.close();
}
}
