import { Injectable } from '@angular/core';
import { HttpParams } from '@angular/common/http';
import { API } from '../url-constants';
import { HttpServices } from './http.service';
import { PositionModel } from '../model/position.model';

@Injectable({
  providedIn: 'root'
})
export class PositionService {
  constructor(
    public http: HttpServices
  ) { }
  getPosition() {
    const httpParams = new HttpParams();
    const requestOptions = {
      params: httpParams
    };
    return this.http.get(API.POSITION, requestOptions);
  }
  postPosition(data: any) {
    const httpParams = new HttpParams();
    const requestOptions = {
      params: httpParams
    };
    return this.http.post(API.CREATEPOSITION, data);
  }
  deletePosition(data: any) {
    //console.log(data)
    const httpParams = new HttpParams();
    const requestOptions = {
      params: httpParams
    };
    return this.http.delete(API.DELETEPOSITION.replace(':_id', data._id));

  }
  putPosition(data: any) {
    const httpParams = new HttpParams();
    const requestOptions = {
      params: httpParams
    };
    return this.http.put(API.PUTPOSITION.replace(':_id', data._id), data);

  }
}
