import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { AssetModelService } from 'src/app/service/assetModel.service';
import Swal from 'sweetalert2';
@Component({
  selector: 'app-edit-model',
  templateUrl: './edit-model.component.html',
  styleUrls: ['./edit-model.component.css']
})
export class EditModelComponent implements OnInit {
  submitted : boolean
  public AssetModel: any;
  public modelType: string;
  duplicate: boolean;
  AssetModel_chackDup: any;
  createdFormModel: FormGroup;
  public olddata: any;

  constructor(
    private formBuilder: FormBuilder,
    private assetModelService: AssetModelService,
    public dialogRef: MatDialogRef<EditModelComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
  ) { }
  ngOnInit() {
    this.AssetModel = this.data.dataKey;
    this.modelType = this.AssetModel.modelType;
    this.AssetModel_chackDup = this.data.dataTable
    this.createdFormModel = this.formBuilder.group({
      modelType: new FormControl({ value: '', disabled: false }, [Validators.required]),
  });
  this.createdFormModel.patchValue({
    modelType:this.modelType,
  })
  this.olddata = this.modelType

  }
  onSubmit() {
    if(this.olddata ==this.createdFormModel.value.modelType){
      this.dialogRef.close();
    }else{
      this.checkDuplicate();
      if (this.duplicate == true || this.createdFormModel.value.modelType === ''  ) {
        this.submitted = false
      } else {
        this.AssetModel.status = true;
        this.submitted = true
        this.AssetModel.modelType = this.createdFormModel.value.modelType;
        this.assetModelService.putAssetModel(this.AssetModel).then(() => {
       });
       Swal.fire({
        position: 'center',
        icon: 'success',
        title: 'Your work has been saved',
        showConfirmButton: false,
        timer: 1000
      })
      this.dialogRef.close();
  }
  }
}
checkDuplicate() {
  for(let i = 0; i < this.AssetModel_chackDup.length; i++){
    if(this.createdFormModel.value.modelType == this.AssetModel_chackDup[i].modelType){
      console.log("Duplicate");
      this.duplicate = true;
      i =this.AssetModel_chackDup.length;
    } else {
      this.duplicate = false;
    }
  }
}
onCancel(){
  this.dialogRef.close();
}
}
