import { Injectable } from '@angular/core';
import { HttpParams, HttpClient } from '@angular/common/http';
import { API } from '../url-constants';
import { HttpServices } from './http.service';

@Injectable({
  providedIn: 'root'
})
export class TypeService {
  constructor(
    public http: HttpServices
  ) { }

  getType() {
    const httpParams = new HttpParams();
    const requestOptions = {
      params: httpParams
    };

    return this.http.get(API.TYPE, requestOptions);
  }
  postType(data: any){
    const httpParams = new HttpParams();
    const requestOptions = {
      params: httpParams
    };
    return this.http.post(API.CREATETYPE, data);
  }
  deleteTypeAsset(data: any) {
    console.log(data)
    const httpParams = new HttpParams();
    const requestOptions = {
      params: httpParams
    };
    return this.http.delete(API.DELETETYPEASSET.replace(':_id', data._id));
  }
  putTypeAsset(data: any) {
    const httpParams = new HttpParams();
    const requestOptions = {
      params: httpParams
    };
    return this.http.put(API.PUTTYPEASSET.replace(':_id', data._id), data);

  }
}
