import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { LoginComponent} from '../app/login/login.component';
import { DataAssetComponent} from './data-asset/components/data-asset/data-asset.component';
import { DataEmployeeComponent} from './employee/components/data-employee/data-employee.component';
import { ManagementComponent} from './management/components/position/management.component';
import { SiteComponent} from './site/components/site/site.component';
import { ChangeadComponent} from './changead/components/chande-ad/chande-ad.component';
import { BrandnormalComponent } from './brand/components/brandnormal/brandnormal.component';
