import { StatusComponent } from './status/components/status/status.component';
import { ForgotPasswordComponent } from './changead/components/forgot-password/forgot-password.component';
import { DetailEmployeeComponent } from './employee/components/detail-employee/detail-employee.component';
import { DetailAssetComponent } from './data-asset/components/detail-asset/detail-asset.component';
import { ListManageComponent } from './list-manage/components/list-manage/list-manage.component';
import { CreateAccountComponent } from './account-manage/components/create-account/create-account.component';
