import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { BrandModel } from 'src/app/model/brand.model';
import { BrandService } from 'src/app/service/brand.service';
import { TypeService } from 'src/app/service/type.service';
import Swal from 'sweetalert2';
@Component({
  selector: 'app-add-brand',
  templateUrl: './add-brand.component.html',
  styleUrls: ['./add-brand.component.css']
})
export class AddBrandComponent implements OnInit {
  submitted: boolean
  brandTable: any;
  duplicate: boolean;
  public type: any;
  createdFormBrand: FormGroup;
  constructor(
    private typeService: TypeService,
    private formBuilder: FormBuilder,
    public brandService: BrandService,
    private router: Router,
    public dialogRef: MatDialogRef<AddBrandComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
  ) { }

  createdForm() {
    this.createdFormBrand = this.formBuilder.group({
      brandName: new FormControl({ value: '', disabled: false }, [Validators.required]),
      brandType: new FormControl({ value: '', disabled: false }, [Validators.required]),
    });
  }
  ngOnInit() {
    this.createdForm()
    this.brandTable = this.data.dataKey;

    this.typeService.getType().then((result) => {
      this.type = result.responseData.data;
    });
  }
  checkDuplicate() {
    for (let i = 0; i < this.brandTable.length; i++) {
      if (this.createdFormBrand.value.brandName == this.brandTable[i].brandName
        && this.createdFormBrand.value.brandType == this.brandTable[i].brandType) {
        console.log("Duplicate");
        this.duplicate = true;
        i = this.brandTable.length;
      } else {
        this.duplicate = false;
      }
    }
  }
  onSubmit() {
    this.checkDuplicate();
    if (this.duplicate == true || this.createdFormBrand.value.brandName === "" ||
      this.createdFormBrand.value.brandType === "") {
      this.submitted = false
    } else {
      this.submitted = true
      this.brandService.postBrand(this.createdFormBrand.value).then(() => {
      });
      Swal.fire({
        position: 'center',
        icon: 'success',
        title: 'Your work has been saved',
        showConfirmButton: false,
        timer: 1000
      })
      this.dialogRef.close();
    }
  }
  onCancel() {
    this.dialogRef.close();
  }

}
