import { Component, Inject, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormControl, FormGroup } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { ActivatedRoute, Router } from '@angular/router';
import { AssetService } from 'src/app/service/asset.service';
import { RepairService } from 'src/app/service/repair.service';
import { StatusService } from 'src/app/service/status.service';
import Swal from 'sweetalert2';


@Component({
  selector: 'app-edit-status-repair',
  templateUrl: './edit-status-repair.component.html',
  styleUrls: ['./edit-status-repair.component.css']
})
export class EditStatusRepairComponent implements OnInit {
  formchange: FormGroup;
  formrepair: FormGroup;
  public dataAsset: any;
  public status: any;
  public repair: any;
  assetTable: any;
  dataSource: MatTableDataSource<any>;
  duplicate: boolean;
  submitted: boolean;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private repairService: RepairService,
    private formBuilder: FormBuilder,
    private statusService: StatusService,
    private assetService: AssetService,
    private router: Router,
    private route: ActivatedRoute,
    public dialogRef: MatDialogRef<EditStatusRepairComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    ) { }

  ngOnInit() {

    this.formrepair = this.formBuilder.group({
      _id: new FormControl({ value: '', disabled: false }),
      remark: new FormControl({ value: '', disabled: false }),
      purchaserepair: new FormControl({ value: '', disabled: false }),
      repairDt: new FormControl({ value: '', disabled: false }),
    });
    this.repairService.getRepair().then((result) => {
      this.repair = result.responseData.data;
    });
    this.dataAsset = this.data.dataKey;
    this.formrepair.patchValue({
      _id: this.dataAsset._id,
      remark: this.dataAsset.remark,
      purchaserepair: this.dataAsset.purchaserepair,
      repairDt: this.dataAsset.repairDt,
    })

  }
  onCancel() {
    this.dialogRef.close();
  }

  onSubmit(){
    this.dataAsset._id = this.formrepair.value._id;
    this.dataAsset.remark = this.formrepair.value.remark;
    this.dataAsset.purchaserepair = this.formrepair.value.purchaserepair;
    this.dataAsset.repairDt = this.formrepair.value.repairDt;
    console.log(this.dataAsset);
    this.repairService.putRepair(this.dataAsset)
   Swal.fire({
        position: 'center',
        icon: 'success',
        title: 'Your work has been saved',
        showConfirmButton: false,
        timer: 1500
      })
      this.dialogRef.close();
  }

  }

