import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { TypeService } from 'src/app/service/type.service';
import Swal from 'sweetalert2';
@Component({
  selector: 'app-edit-type',
  templateUrl: './edit-type.component.html',
  styleUrls: ['./edit-type.component.css']
})
export class EditTypeComponent implements OnInit {
  submitted : boolean
  public type: any;
  public typeName: string;
  duplicate: boolean;
  type_chackDup: any;
  createdFormTypeasset: FormGroup;
  olddata:any;
  constructor(
    private formBuilder: FormBuilder,
    private typeService: TypeService,
    public dialogRef: MatDialogRef<EditTypeComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
  ) { }
  ngOnInit(){
    this.type = this.data.dataKey;
    this.typeName = this.type.typeName;
    this.type_chackDup = this.data.dataTable
    this.createdFormTypeasset = this.formBuilder.group({
      typeName: new FormControl({ value: this.typeName, disabled: false }, [Validators.required]),
  });
  this.createdFormTypeasset.patchValue({
    typeName:this.typeName,
  })
  this.olddata =this.typeName
  }
  onSubmit() {
    if(this.olddata ==this.createdFormTypeasset.value.typeName){
      this.dialogRef.close();
    }else{
      this.checkDuplicate();
          if (this.duplicate == true || this.createdFormTypeasset.value.typeName === '' ) {
            this.submitted = false
          } else {
            this.type.status = true;
            this.submitted = true
            this.type.typeName = this.createdFormTypeasset.value.typeName;
            this.typeService.putTypeAsset(this.type).then(() => {
          });
          Swal.fire({
            position: 'center',
            icon: 'success',
            title: 'Your work has been saved',
            showConfirmButton: false,
            timer: 1000
          })
          this.dialogRef.close();
          }
    }
}

checkDuplicate() {
  for(let i = 0; i < this.type_chackDup.length; i++){
    if(this.createdFormTypeasset.value.typeName == this.type_chackDup[i].typeName){
      console.log("Duplicate");
      this.duplicate = true;
      i =this.type_chackDup.length;
    } else {
      this.duplicate = false;
    }
  }
}

onCancel(){
  this.dialogRef.close();
}
}

