import { Injectable } from '@angular/core';
import { HttpParams, HttpClient } from '@angular/common/http';
import { API } from '../url-constants';
import { HttpServices } from './http.service';

@Injectable({
  providedIn: 'root'
})
export class StatusService {
  constructor(
    public http: HttpServices
  ) { }
  getStatus() {
    const httpParams = new HttpParams();
    const requestOptions = {
      params: httpParams
    };
    return this.http.get(API.STATUS, requestOptions);
  }

  postStatus(data: any){
    const httpParams = new HttpParams();
    const requestOptions = {
      params: httpParams
    };
    return this.http.post(API.CREATETYSTATUS, data);
  }

  deleteStatus(data: any){
  const httpParams = new HttpParams();
  const requestOptions = {
    params: HttpParams
  };
  return this.http.delete(API.DELETESTATUS.replace(':_id', data._id));
}
  putStatus(data: any) {
  const httpParams = new HttpParams();
  const requestOptions = {
    params: httpParams
  };
  return this.http.put(API.PUTSTATUS.replace(':_id', data._id), data);
}
getStatusFree() {
  const httpParams = new HttpParams();
  const requestOptions = {
    params: httpParams
  };
  return this.http.get(API.STATUSFREE, requestOptions);
}

getStatusUsed() {
  const httpParams = new HttpParams();
  const requestOptions = {
    params: httpParams
  };
  return this.http.get(API.STATUSUSED, requestOptions);
}

}
