    sumprice(data : KeyboardEvent): boolean{

    const regex: RegExp = new RegExp(/^\d*\.?\d{0,3}$/g);
    let f = data.key.indexOf(".");

    if (f != -1) {
      this.countDecimal = this.countDecimal + 1 ;
      this.decipeice = data.key;
    } else {
      console.log(this.decipeice)
      console.log(this.countDecimal)
      if (this.decipeice.length > 0) {
        // this.decipeice = this.decipeice.replace(".","");
        if (data.key === "Backspace") {
          this.decipeice = this.decipeice.replace("Backspace","");
          if (this.decipeice == ".") {
            this.countDecimal = 0;
          }
          this.countDecimal = this.decipeice.length - 1;

          this.decipeice = this.decipeice.slice(0, this.countDecimal);
          console.log(this.countDecimal);
          console.log(this.decipeice);
        } else {
          if (this.decipeice.length < 3) {
            this.decipeice += data.key;
          }
          this.countDecimal = this.countDecimal + 1 ;
        }
      } else {
        this.frontData += data.key;
        if (data.key === "Backspace") {
          this.frontData = this.frontData.replace("Backspace","");
          this.frontData = this.frontData.slice(0, this.frontData.length - 1);
        }
      }
    }
    const price = this.frontData + this.decipeice;
    let test = parseFloat(price)
    let test2 = (test*7/100)
    let c = test+test2
    console.log("================");
    console.log(test);

    this.groupdataasset.patchValue({
      priceVat: test2.toFixed(2),
      totalPrice: c.toFixed(2)
    })
      console.log(price)
      console.log(this.decipeice)
      console.log(this.frontData)
      if (this.countDecimal > 3) {
        return false
      } else {
        return true
      }
  }

