export class DataAssetComponent implements OnInit {
  foods: Food[] = [
    {value: 'มี' },
    {value: 'ไม่มี'},
  ];
  listData: MatTableDataSource<any>;
  displayedColumns: string[] = ['no','assetCode','type','brand','cpu','remark','serialNumber','state','whoused','boi','updateBy','updateDt','action'];
  dataSource: MatTableDataSource<any>;
  dataSourceA: MatTableDataSource<any>;
  color = 'gray'
  public formDate: FormGroup;
  public search: FormGroup;
  public filter: String;
  public testbrand: any[];

  renderedData: any;
  groupdataasset: FormGroup;
  public newProfile: any;
  public roleAccount:boolean;
  public form: any;
  public dataType: any;
  public dataBrand: any;
  public dataAsset : any = { count: 0, rows: [] };
  public dataModel : any;
  public StatusName = true;
  public status: any;
  public emType: any;
  assetCode = new dataassetModel();
  public statedetailas: any;
  public dataposition: any;
  public dataSite: any;
  Listasset :any = [];

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  datepipe: any;


  constructor(
    // ----------filer---------
    private formBuilder: FormBuilder,
    private positionService: PositionService,
    private siteService: SiteService,
    private router: Router,
    public dialog : MatDialog,
    private assetService: AssetService,
    private brandService: BrandService,
    private typeService: TypeService,
    private assetModelService: AssetModelService,
    private statusService: StatusService,
    private employeeTypeService: EmployeeTypeService
  ){}

  selectedRowIndex = -1;
  highlight(row: { id: number; }){
    this.selectedRowIndex = row.id;
}

createdForm() {
  this.formDate = this.formBuilder.group({
    startPeriod: new FormControl({ value:'', disabled: false }),
    endPeriod: new FormControl({ value:'', disabled: false }),
  });
  this.search = this.formBuilder.group({
    searchText: new FormControl({ value:'', disabled: false }),
  });
}
  ngOnInit() {
    const profile = localStorage.getItem('Profile')
    this.newProfile = JSON.parse(profile)
    if(this.newProfile.role == 'ADMIN'){
      this.roleAccount = true
        }
    this.refresh()
    this.createdForm()
    this.positionService.getPosition().then(result => {
      this.dataposition = result.responseData.data;
    });
    this.siteService.getSite().then(result => {
      this.dataSite = result.responseData.data;
    });
    this.brandService.getBrand().then(result => {
      this.dataBrand = result.responseData.data;
    });
    this.employeeTypeService.getEmployType().then(result => {
      this.emType = result.responseData.data;
    });
    this.typeService.getType().then(result => {
      this.dataType = result.responseData.data;
    });
    this.assetModelService.getAssetModel().then(result => {
      this.dataModel = result.responseData.data;
    });
    this.statusService.getStatus().then(result =>{
      this.status = result.responseData.data;
    })
  }

  public doFilter = (value: string) => {
    if(value=="มี"){
      value="true"
    }
    if(value=="ไม่มี"){
      value="false"
    }
    this.dataSource.filter = value.trim().toLocaleLowerCase();
    this.dataSourceA.filter = value.trim().toLocaleLowerCase();
  }
  public typeFilter = (value: string) => {
    this.dataSource.filter = value.trim().toLocaleLowerCase();
    this.dataSourceA.filter = value.trim().toLocaleLowerCase();
  }

  public brandFilter = (a) => {
    this.testbrand = new Array()
    this.assetService.getlistasset().then(result => {
      this.dataAsset = result;
      let c = a._id
      console.log(c);
      for(let i =0; i<this.dataAsset.length; i++){
        if(c==this.dataAsset[i].brand){
          const s =this.dataAsset[i].brandid
          let d = this.dataAsset[i]
          this.testbrand.push(d)
        }
        else{
        }
      this.dataSource = new MatTableDataSource(this.testbrand);
      this.dataSourceA = new MatTableDataSource(this.testbrand);
      this.dataSourceA.connect().subscribe(d =>{this.renderedData = d});
    }
  })
  }

  public modelFilter = (value: string) => {
    this.dataSource.filter = value.trim().toLocaleLowerCase();
    this.dataSourceA.filter = value.trim().toLocaleLowerCase();
  }
  public stateFilter = (value: string) => {
    this.dataSource.filter = value.trim().toLocaleLowerCase();
    this.dataSourceA.filter = value.trim().toLocaleLowerCase();
  }
  public boiFilter = (value: string) => {
    if(value=="มี"){
      value="true"
    }
    if(value=="ไม่มี"){
      value="false"
    }
    this.dataSource.filter = value.trim().toLocaleLowerCase();
    this.dataSourceA.filter = value.trim().toLocaleLowerCase();
  }
  openDialog() {
    this.dialog.open(DataAssetPopupComponent, {
      data: {
        dataKey: this.dataAsset
      }
    }).afterClosed().subscribe(result => {
      this.refresh();
    });
  }
  openDialogchange(data) {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.data = data;
    this.dialog.open(ChangeStatusComponent, {
      height: '80%',
      width: '70%',
      data: {
        dataKey: data
      }
    }).afterClosed().subscribe(result => {
      this.refresh();
    });
  }
  openDialogEditAsset(data) {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.data = data;
    console.log(data)
    this.dialog.open(EditAsComponent, {
      data: {
        dataKey: data,
        dataTable: this.dataAsset,
      }
    }).afterClosed().subscribe(result => {
      this.refresh();
    });
  }

  exportExcel() {
    const assetListExport:any[] = new Array()
    if(this.renderedData == ''){

      Swal.fire({
        icon: 'error',
        text: 'ไม่พบข้อมูล',
      })
    } else{
      for (let i = 0;  i < this.renderedData.length; i++){
        this.Listasset.push("'" + this.renderedData[i]._id + "'");
    }

    console.log(this.Listasset);
    this.assetService.reportAsset(this.Listasset).then(result => {
      this.Listasset = new Array()
      console.log('result');
      console.log(result);
      let  listAsset : any[] = new Array()
      listAsset = result
        import('xlsx').then(xlsx => {
        for (let i = 0; i < listAsset.length; i++ ){
          if(listAsset[i].boi==true){
            listAsset[i].boi ='มี'
          }
          if(listAsset[i].boi==false){
            listAsset[i].boi ='ไม่มี'
          }
        assetListExport.push({
            AssetCode: listAsset[i].assetCode,
            BrandName: listAsset[i].brandName,
            TypeName: listAsset[i].typeName,
            CPU: listAsset[i].modelType,
            Color: listAsset[i].color,
            SerialNumber: listAsset[i].serialNumber,
            Status: listAsset[i].StatusName,
            PurchaseDt: listAsset[i].purchaseDt? moment(new Date(listAsset[i].purchaseDt)).format('DD/MM/YYYY') : '',
            InsuranceDt: listAsset[i].insuranceDt? moment(new Date(listAsset[i].insuranceDt)).format('DD/MM/YYYY') : '',
            Remark: listAsset[i].remark,
            Boi: listAsset[i].boi,
            PurchaseNo:listAsset[i].purchaseNo,
            InsuranceTerm:listAsset[i].insuranceTerm,
            Remarkrepair: listAsset[i].remarkrepair,
            RepairDt:listAsset[i].RepairDt ? moment(new Date(listAsset[i].RepairDt)).format('DD/MM/YYYY') : '',
            RepairAt:listAsset[i].repairAt,
            Pricerepair:listAsset[i].pricerepair == 'NaN' ? '':listAsset[i].pricerepair,
            Pricerepairvat:listAsset[i].pricerepairvat == 'NaN' ? '':listAsset[i].pricerepairvat,
            Totalpricerepair:listAsset[i].totalpricerepair == 'NaN' ? '':listAsset[i].totalpricerepair,
            Price:listAsset[i].price == 'NaN' ? '':listAsset[i].price,
            PriceVat:listAsset[i].priceVat == 'NaN' ? '':listAsset[i].priceVat,
            TotalPrice: listAsset[i].totalPrice == 'NaN' ? '':listAsset[i].totalPrice,
            Purchaserepair: listAsset[i].purchaserepair ? moment(new Date(listAsset[i].purchaserepair)).format('DD/MM/YYYY') : '',
            Prefix: listAsset[i].prefix,
            Firstname: listAsset[i].firstname,
            Lastname: listAsset[i].lastname,
            Nickname: listAsset[i].nickname,
            TypeEmployee: listAsset[i].typeEmployee,
            Position: listAsset[i].position,
            Site: listAsset[i].siteType,
            StartingDate: listAsset[i].workStart? moment(new Date(listAsset[i].workStart)).format('DD/MM/YYYY') : '',
            ResignationDate: listAsset[i].workEnd? moment(new Date(listAsset[i].workEnd)).format('DD/MM/YYYY') : '',
            UpdateBy: listAsset[i].updateBy,
            UpdateDt: listAsset[i].updateDt ? moment(new Date(listAsset[i].updateDt)).format('DD/MM/YYYY HH:mm') : ''
        });
      }
        const worksheet = xlsx.utils.json_to_sheet(assetListExport);
        const workbook = { Sheets: { 'data': worksheet },SheetNames: ['data'] };
        const excelBuffer: any = xlsx.write(workbook, { bookType: 'xlsx', type: 'array'});
        this.saveAsExcelFile(excelBuffer);
      });
    })
  }
}
  saveAsExcelFile(buffer: any): void {
    import('file-saver').then(FileSaver => {
      const EXCEL_TYPE = 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8';
      const EXCEL_EXTENSION = '.xlsx';
      const data: Blob = new Blob([buffer], {
        type: EXCEL_TYPE
      });
      FileSaver.saveAs(data, 'Asset' + '_export_report_' + new Date().getTime() + EXCEL_EXTENSION);
    });
  }

  onclickDate() {
    const startdate : any =  this.formDate.value.startPeriod;
    const Enddate : any = this.formDate.value.endPeriod;
    const dataTest : any  = this.dataAsset
    const total : any = [];
    dataTest.forEach(element => {
      const createdate = new Date(element.updateDt)
      if(moment(createdate).isSameOrAfter(startdate) && moment(createdate ).isSameOrBefore(Enddate) ){
        total.push(element)
      }
    });
      this.dataSource = new MatTableDataSource(total);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
      this.dataSourceA = new MatTableDataSource(total);
      this.dataSourceA.connect().subscribe(d => this.renderedData = d);
  }
  public statedetailasset (row) {
    this.statedetailas = row
    this.router.navigate(['statedetail', {data :JSON.stringify(this.statedetailas) }])
  }
  onclickListManage() {
    this.router.navigate(['listManage', {}])
  }
  onclickEmployee() {
    this.router.navigate(['employee',{}])
  }
  onclickAsset() {
    this.router.navigate(['data-asset',{}])
  }
  onclickLogin() {
    this.router.navigate(['login',{}])
  }
  onclickdataadmin() {
    this.router.navigate(['admin',{}])
  }
  onclickaccount() {
    this.router.navigate(['list-account',{}])
  }
  clickSearch() {
    this.router.navigate(['changead',{}])
  }
  clickeditStatus() {
    this.router.navigate(['editStatus',{}])
  }
clickdelete(data) {
  Swal.fire({
    title: 'Delete Asset',
    text: "Are you sure you want to delete this Asset?",
    icon: 'question',
    showCancelButton: true,
    confirmButtonColor: 'rgb(70, 0, 0)',
    cancelButtonColor: '#696969',
  }).then((result) => {
    if (result.isConfirmed) {
      this.assetService.deleteAsset(data).then(() => {
        this.refresh()
      });
      Swal.fire(
            'Deleted!',
            'Your file has been deleted.',
            'success'
          )
    }
  })
}
refresh(){
  this.assetService.getlistasset().then(result => {
    this.dataAsset = result;
    const a = result

    this.dataSource = new MatTableDataSource(this.dataAsset);
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
    this.dataSourceA = new MatTableDataSource(this.dataAsset);
    this.dataSourceA.connect().subscribe(d => this.renderedData = d);
  })
}
clearFilters(){
  this.refresh()
    this.formDate.patchValue({
      startPeriod: null,
      endPeriod: null,
  })
  this.search.patchValue({
    searchText: null,
  })
}
logout() {
  localStorage.removeItem('Authorization');
  this.router.navigate(['login', {}])
}
}
