import { Component, OnInit, ViewChild } from '@angular/core';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Router } from '@angular/router';
import { TypeService } from 'src/app/service/type.service';
import { AddTypeComponent } from '../add-type/add-type.component';
import { EditTypeComponent } from '../edit-type/edit-type.component';
import Swal from 'sweetalert2';
interface manage {
  value: string;
  viewValue: string;
}

@Component({
  selector: 'app-typenormal',
  templateUrl: './typenormal.component.html',
  styleUrls: ['./typenormal.component.css']
})

export class TypenormalComponent implements OnInit {

  listData: MatTableDataSource<any>;
  displayedColumns: string[] = ['no', 'type','updateDt','updateBy','action'];
  dataSource: MatTableDataSource<any>;

  public dataType: any;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private router: Router,
    public dialog : MatDialog,
    private typeService: TypeService,
  ){}

  selectedRowIndex = -1;
  highlight(row: { id: number; }){
    this.selectedRowIndex = row.id;
}

manages: manage[] = [
  {value: '1', viewValue: 'Position'},
  {value: '2', viewValue: 'Site'},
  {value: '3', viewValue: 'Brand'},
  {value: '4', viewValue: 'CPU'},
  {value: '5', viewValue: 'Asset Type'},
  {value: '6', viewValue: 'Employee Type'},
  {value: '7', viewValue: 'Status'},
];

  ngOnInit(): void {
    this.typeService.getType().then(result => {
      console.log(result)
      this.dataType = result.responseData.data;
      console.log(this.dataType)

      this.dataSource = new MatTableDataSource(this.dataType);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    })
  }
  public doFilter = (value: string) => {
    this.dataSource.filter = value.trim().toLocaleLowerCase();
  }
  refresh() {
    this.typeService.getType().then(result => {
      this.dataType = result.responseData.data;
      this.dataSource = new MatTableDataSource(this.dataType);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    })
  }

  openDialog() {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.data = this.dataType;
    this.dialog.open(AddTypeComponent, {
      data: {
        dataKey: this.dataType
      }
    }).afterClosed().subscribe(result => {
      this.refresh();
    });
  }

  openDialogEdittype(data) {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.data = data;
    this.dialog.open(EditTypeComponent, {
      data: {
        dataKey: data,
        dataTable: this.dataType
      }
    }).afterClosed().subscribe(result => {
      this.refresh();
    });
  }
  clickdelete(data) {
    Swal.fire({
      title: 'Delete TypeAsset',
      text: "Are you sure you want to delete this TypeAsset?",
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: 'rgb(70, 0, 0)',
      cancelButtonColor: '#696969',
    }).then((result) => {
      if (result.isConfirmed) {
        this.typeService.deleteTypeAsset(data).then(() => {
        });
       this.refresh();
        Swal.fire(
          'Deleted!',
          'Your file has been deleted.',
          'success'
        )
      }
    })
  }

  onChange(value){
    switch(value){
      case '1' : this.router.navigate(['management',{}]); break;
      case '2' : this.router.navigate(['site',{}]); break;
      case '3' : this.router.navigate(['brand',{}]); break;
      case '4' : this.router.navigate(['model',{}]); break;
      case '5' : this.router.navigate(['type',{}]); break;
      case '6' : this.router.navigate(['emtype',{}]); break;
      case '7' : this.router.navigate(['status',{}]); break;
    }
  }
  onclickListManage(){
    this.router.navigate(['listManage', {}])
  }
  onclickEmployee() {
    this.router.navigate(['employee',{}])
  }
  onclickAsset() {
    this.router.navigate(['data-asset',{}])
  }
  onclickdataadmin() {
    this.router.navigate(['admin',{}])
  }
  onclickaccount() {
    this.router.navigate(['list-account',{}])
  }
  logout() {
    localStorage.removeItem('Authorization');
    this.router.navigate(['login', {}])
  }
}
