  // --------------------------- decimal sale --------------------------
  private countDecimal = 0;
  private decipeice = "";
  private frontData = "";
  private isEdit = false;

  sumpricesale(sale : KeyboardEvent): boolean{
    const regex: RegExp = new RegExp(/^\d*\.?\d{0,3}$/g);
    let f = sale.key.indexOf(".");

    if (f != -1) {
      this.countDecimal = this.countDecimal + 1 ;
      this.decipeice = sale.key;
    } else {
      if (this.decipeice.length > 0) {
        // this.decipeice = this.decipeice.replace(".","");
        if (sale.key === "Backspace") {
          this.decipeice = this.decipeice.replace("Backspace","");
          if (this.decipeice == ".") {
            this.countDecimal = 0;
          }
          this.countDecimal = this.decipeice.length - 1;

          this.decipeice = this.decipeice.slice(0, this.countDecimal);
        } else {
          if (this.decipeice.length < 3) {
            this.decipeice += sale.key;
          }
          this.countDecimal = this.countDecimal + 1 ;
        }
      } else {
        this.frontData += sale.key;
        if (sale.key === "Backspace") {
          this.frontData = this.frontData.replace("Backspace","");
          this.frontData = this.frontData.slice(0, this.frontData.length - 1);
        }
      }
    }
    const salePrice = this.frontData + this.decipeice;
    let sale1 = parseFloat(salePrice)
    let sale2 = (sale1*7/100)
    let c = sale1+sale2
    console.log("================");
    console.log(sale1);

    this.formchange.patchValue({
      salePricevat: sale2.toFixed(2),
      salePricetotal: c.toFixed(2)
    })
      console.log(salePrice)
      console.log(this.decipeice)
      console.log(this.frontData)
      if (this.countDecimal > 3) {
        return false
      } else {
        return true
      }
  }
