import { Injectable } from '@angular/core';
import { HttpParams, HttpClient } from '@angular/common/http';
import { API } from '../url-constants';
import { HttpServices } from './http.service'

@Injectable({
  providedIn: 'root'
})
export class AssetModelService {

  constructor(
    public http: HttpServices
  ) { }
  getAssetModel() {
    const httpParams = new HttpParams();
    const requestOptions = {
      params: httpParams
    };
    return this.http.get(API.ASSETMODEL, requestOptions);
  }

  postAssetModel(data: any){
    const httpParams = new HttpParams();
    const requestOptions = {
      params: httpParams
    };
    return this.http.post(API.CREATEASSETMODEL, data);
  }
  deleteAssetModel(data: any) {
    console.log(data)
    const httpParams = new HttpParams();
    const requestOptions = {
      params: httpParams
    };
    return this.http.delete(API.DELETEASSETMODEL.replace(':_id', data._id));

  }
  putAssetModel(data: any) {
    const httpParams = new HttpParams();
    const requestOptions = {
      params: httpParams
    };
    return this.http.put(API.PUTASSETMODEL.replace(':_id', data._id), data);

  }

}
