import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { EmployeeTypeModel } from 'src/app/model/employeeType.model';
import { EmployeeTypeService } from 'src/app/service/employee-type.service';
import Swal from 'sweetalert2';
@Component({
  selector: 'app-emtype-add',
  templateUrl: './emtype-add.component.html',
  styleUrls: ['./emtype-add.component.css']
})
export class EmtypeAddComponent implements OnInit {
  typeEmTable: any;
  duplicate: boolean;
  submitted: boolean
  createdFormEmtypt: FormGroup;
  constructor(
    private formBuilder: FormBuilder,
    public employeetypeService: EmployeeTypeService,
    public dialogRef: MatDialogRef<EmtypeAddComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
  ) {}
  createdForm() {
    this.createdFormEmtypt = this.formBuilder.group({
      emType: new FormControl({ value: '', disabled: false }, [Validators.required]),
    });
  }
  ngOnInit(){
    this.createdForm()
    this.typeEmTable = this.data.dataKey;
  }
  checkDuplicate() {
    for(let i = 0; i < this.typeEmTable.length; i++){
      if(this.createdFormEmtypt.value.emType == this.typeEmTable[i].emType){
        console.log("Duplicate");
        this.duplicate = true;
        i =this.typeEmTable.length;
      } else {
        this.duplicate = false;
      }
    }
  }

  onSubmit(){
    this.checkDuplicate();
    if (this.duplicate == true || this.createdFormEmtypt.value.emType === "") {
      this.submitted = false
    } else {
    this.employeetypeService.postTypeEm(this.createdFormEmtypt.value).then(() => {
    });
    Swal.fire({
      position: 'center',
      icon: 'success',
      title: 'Your work has been saved',
      showConfirmButton: false,
      timer: 1000
    })
    this.dialogRef.close();
  }
}
onCancel(){
  this.dialogRef.close();
}

}
