import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { StatusService } from 'src/app/service/status.service';
import Swal from 'sweetalert2';
@Component({
  selector: 'app-edit-status',
  templateUrl: './edit-status.component.html',
  styleUrls: ['./edit-status.component.css']
})
export class EditStatusComponent implements OnInit {
  submitted : boolean
  public status: any;
  public StatusName: string;
  status_chackDup: any;
  duplicate: boolean;
  olddata:any;
  createdFormStatus: FormGroup;
  constructor(
    private formBuilder: FormBuilder,
    private statusService: StatusService,
    public dialogRef: MatDialogRef<EditStatusComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
  ) { }

  ngOnInit(){
    this.status = this.data.dataKey;
    this.StatusName = this.status.StatusName
    this.status_chackDup = this.data.dataTable
    this.createdFormStatus = this.formBuilder.group({
      StatusName: new FormControl({ value: '', disabled: false }, [Validators.required]),
  });
  this.createdFormStatus.patchValue({
    StatusName:this.StatusName,
  })
  this.olddata = this.StatusName
  }

checkDuplicate() {
  for(let i = 0; i < this.status_chackDup.length; i++){
    if(this.createdFormStatus.value.StatusName == this.status_chackDup[i].StatusName){
      console.log("Duplicate");
      this.duplicate = true;
      i =this.status_chackDup.length;
    } else {
      this.duplicate = false;
    }
  }
}
onCancel(){
  this.dialogRef.close();
}
onSubmit() {
  if(this.olddata ==this.createdFormStatus.value.StatusName ){
    this.dialogRef.close();
  }else{
    this.checkDuplicate();
      if (this.duplicate == true ||  this.createdFormStatus.value.StatusName === '' ) {
        this.submitted = false
      } else {
        this.submitted = true
        this.status.StatusName = this.createdFormStatus.value.StatusName;
        this.statusService.putStatus(this.status).then(() => {
      });
      Swal.fire({
        position: 'center',
        icon: 'success',
        title: 'Your work has been saved',
        showConfirmButton: false,
        timer: 1000
      })
      this.dialogRef.close();
    }
  }
}
}
