import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { SiteModel } from 'src/app/model/site.model';
import { SiteService } from 'src/app/service/site.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-edit-site',
  templateUrl: './edit-site.component.html',
  styleUrls: ['./edit-site.component.css']
})
export class EditSiteComponent implements OnInit {
  submitted : boolean
  dataSite: any;
  public site: any;
  public siteType: string;
  duplicate: boolean;
  edit : boolean;
  site_chackDup: any;
  createdFormSite: FormGroup;
  public olddata:any

  constructor(
    private formBuilder: FormBuilder,
    private siteService: SiteService,
    public dialogRef: MatDialogRef<EditSiteComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
  ) { }
  ngOnInit() {
    this.site = this.data.dataKey;
    this.siteType = this.site.siteType
    this.site_chackDup = this.data.dataTable
    this.createdFormSite = this.formBuilder.group({
      siteType: new FormControl({ value: '', disabled: false }, [Validators.required]),
  });
  this.createdFormSite.patchValue({
    siteType:this.siteType,
  })

  this.olddata = this.siteType
  }
  onSubmit() {
    if(this.olddata ==this.createdFormSite.value.siteType){
      this.dialogRef.close();
    }else{
      this.checkDuplicate();
          if (this.duplicate == true || this.createdFormSite.value.siteType === '' ) {
            this.submitted = false
          } else {
            this.submitted = true
            this.site.siteType = this.createdFormSite.value.siteType;
            this.siteService.putSite(this.site).then(() => {
          });
          Swal.fire({
            position: 'center',
            icon: 'success',
            title: 'Your work has been saved',
            showConfirmButton: false,
            timer: 1000
          })
          this.dialogRef.close();
          }
    }
}
checkDuplicate() {
  for(let i = 0; i < this.site_chackDup.length; i++){
    if(this.createdFormSite.value.siteType == this.site_chackDup[i].siteType){
      console.log("Duplicate");
      this.duplicate = true;
      i = this.site_chackDup.length;
    } else {
      this.duplicate = false;
    }
  }
}
onCancel(){
  this.dialogRef.close();
}
}
