import { Component, OnInit, ViewChild } from '@angular/core';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Router } from '@angular/router';
import { EmployeeTypeService } from 'src/app/service/employee-type.service';
import { TypeService } from 'src/app/service/type.service';
import { EmtypeAddComponent } from '../emtype-add/emtype-add.component';
import { EmtypeEditComponent } from '../emtype-edit/emtype-edit.component';
import Swal from 'sweetalert2';
interface manage {
  value: string;
  viewValue: string;
}

@Component({
  selector: 'app-em-type',
  templateUrl: './em-type.component.html',
  styleUrls: ['./em-type.component.css']
})

export class EmTypeComponent implements OnInit {

  listData: MatTableDataSource<any>;
  displayedColumns: string[] = ['no', 'typeEmployee','updateDt','updateBy','action'];
  dataSource: MatTableDataSource<any>;

  public dataTypeEm: any;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private router: Router,
    public dialog : MatDialog,
    private employeeTypeService: EmployeeTypeService
  ){}

  selectedRowIndex = -1;
  highlight(row: { id: number; }){
    this.selectedRowIndex = row.id;
}
manages: manage[] = [
  {value: '1', viewValue: 'Position'},
  {value: '2', viewValue: 'Site'},
  {value: '3', viewValue: 'Brand'},
  {value: '4', viewValue: 'CPU'},
  {value: '5', viewValue: 'Asset Type'},
  {value: '6', viewValue: 'Employee Type'},
  {value: '7', viewValue: 'Status'},
];

  ngOnInit(): void {
    this.employeeTypeService. getEmployType().then(result => {
      this.dataTypeEm = result.responseData.data;
      this.dataSource = new MatTableDataSource(this.dataTypeEm);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    })
  }
  openDialog() {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.data = this.dataTypeEm;
    this.dialog.open(EmtypeAddComponent, {
      data: {
        dataKey: this.dataTypeEm
      }
    }).afterClosed().subscribe(result => {
      this.refresh();
    });
  }

  openDialogEdittype(data) {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.data = data;
    console.log(data)
    this.dialog.open(EmtypeEditComponent, {
      data: {
        dataKey: data,
        dataTable: this.dataTypeEm
      }
    }).afterClosed().subscribe(result => {
      this.refresh();
    });
  }

  public doFilter = (value: string) => {
    this.dataSource.filter = value.trim().toLocaleLowerCase();
  }
  clickdelete(data) {
    Swal.fire({
      title: 'Delete TypeEmployee',
      text: "Are you sure you want to delete this TypeEmployee?",
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: 'rgb(70, 0, 0)',
      cancelButtonColor: '#696969',
    }).then((result) => {
      if (result.isConfirmed) {
        this.employeeTypeService.deleteTypeEm(data).then(() => {
        });
      this.refresh()
        Swal.fire(
          'Deleted!',
          'Your file has been deleted.',
          'success'
        )
      }
    })
  }
  refresh() {
    this.employeeTypeService. getEmployType().then(result => {
      this.dataTypeEm = result.responseData.data;
      this.dataSource = new MatTableDataSource(this.dataTypeEm);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    })
  }
  onChange(value){
    switch(value){
      case '1' : this.router.navigate(['management',{}]); break;
      case '2' : this.router.navigate(['site',{}]); break;
      case '3' : this.router.navigate(['brand',{}]); break;
      case '4' : this.router.navigate(['model',{}]); break;
      case '5' : this.router.navigate(['type',{}]); break;
      case '6' : this.router.navigate(['emtype',{}]); break;
      case '7' : this.router.navigate(['status',{}]); break;
    }
  }
  onclickListManage(){
    this.router.navigate(['listManage', {}])
  }
  onclickEmployee() {
    this.router.navigate(['employee',{}])
  }
  onclickAsset() {
    this.router.navigate(['data-asset',{}])
  }
  onclickdataadmin() {
    this.router.navigate(['admin',{}])
  }
  onclickaccount() {
    this.router.navigate(['list-account',{}])
  }
  logout() {
    localStorage.removeItem('Authorization');
    this.router.navigate(['login', {}])
  }
}
