import { Component, OnInit, ViewChild } from '@angular/core';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Router } from '@angular/router';
import { AssetModelService } from 'src/app/service/assetModel.service';
import { AddModelComponent } from '../add-model/add-model.component';
import { EditModelComponent } from '../edit-model/edit-model.component';
import Swal from 'sweetalert2';
interface manage {
  value: string;
  viewValue: string;
}
@Component({
  selector: 'app-modelnormal',
  templateUrl: './modelnormal.component.html',
  styleUrls: ['./modelnormal.component.css']
})
export class ModelnormalComponent implements OnInit {

  listData: MatTableDataSource<any>;
  displayedColumns: string[] = ['no', 'cpu','updateDt','updateBy','action'];
  dataSource: MatTableDataSource<any>;
  public AssetModel: any;
  public position: any;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private router: Router,
    public dialog : MatDialog,
    private assetModelService: AssetModelService,
  ){}

  selectedRowIndex = -1;
  highlight(row: { id: number; }){
    this.selectedRowIndex = row.id;
}

manages: manage[] = [
  {value: '1', viewValue: 'Position'},
  {value: '2', viewValue: 'Site'},
  {value: '3', viewValue: 'Brand'},
  {value: '4', viewValue: 'CPU'},
  {value: '5', viewValue: 'Asset Type'},
  {value: '6', viewValue: 'Employee Type'},
  {value: '7', viewValue: 'Status'},
];

  ngOnInit(){
      this.assetModelService.getAssetModel().then(result => {
      this.AssetModel = result.responseData.data;
      this.dataSource = new MatTableDataSource(this.AssetModel);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    })
  }
  refresh() {
    this.assetModelService.getAssetModel().then(result => {
      this.AssetModel = result.responseData.data;
      this.dataSource = new MatTableDataSource(this.AssetModel);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    })
  }

  clickdelete(data) {
    Swal.fire({
      title: 'Delete Model',
      text: "Are you sure you want to delete this Model?",
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: 'rgb(70, 0, 0)',
      cancelButtonColor: '#696969',
    }).then((result) => {
      if (result.isConfirmed) {
        this.assetModelService.deleteAssetModel(data).then(() => {
        });
        this.refresh();
        Swal.fire(
          'Deleted!',
          'Your file has been deleted.',
          'success'
        )
      }
    })
  }
  public doFilter = (value: string) => {
    this.dataSource.filter = value.trim().toLocaleLowerCase();
  }

  openDialog(){
    const dialogConfig = new MatDialogConfig();
    dialogConfig.data = this.AssetModel;
    this.dialog.open(AddModelComponent, {
      data: {
        dataKey: this.AssetModel
      }
    }).afterClosed().subscribe(result => {
      this.refresh();
    });
  }

  openDialogEditmodel(data) {
  const dialogConfig = new MatDialogConfig();
  dialogConfig.data = data;
  console.log(data)
  this.dialog.open(EditModelComponent, {
    data: {
      dataKey: data,
      dataTable: this.AssetModel
    }
  }).afterClosed().subscribe(result => {
    this.refresh();
  });
}

onChange(value){
  switch(value){
    case '1' : this.router.navigate(['management',{}]); break;
    case '2' : this.router.navigate(['site',{}]); break;
    case '3' : this.router.navigate(['brand',{}]); break;
    case '4' : this.router.navigate(['model',{}]); break;
    case '5' : this.router.navigate(['type',{}]); break;
    case '6' : this.router.navigate(['emtype',{}]); break;
    case '7' : this.router.navigate(['status',{}]); break;
  }
}
onclickListManage(){
  this.router.navigate(['listManage', {}])
}
onclickEmployee() {
  this.router.navigate(['employee',{}])
}
onclickAsset() {
  this.router.navigate(['data-asset',{}])
}
onclickdataadmin() {
  this.router.navigate(['admin',{}])
}
onclickchangead() {
  this.router.navigate(['changead',{}])
}
onclickaccount() {
  this.router.navigate(['list-account',{}])
}
logout() {
  localStorage.removeItem('Authorization');
  this.router.navigate(['login', {}])
}
}

