import { Component, Inject, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { EmployeeTypeService } from 'src/app/service/employee-type.service';
import { EmployeeService } from 'src/app/service/employee.service';
import { PositionService } from 'src/app/service/position.service';
import { SiteService } from 'src/app/service/site.service';
import { PopupAssetComponent } from '../popup-asset/popup-asset.component';
import Swal from 'sweetalert2';
import { EmployeeAssetService } from 'src/app/service/employee-asset.service';
import { StatusService } from 'src/app/service/status.service';
import { HistoryassetService } from 'src/app/service/historyasset.service';
import { AssetService } from 'src/app/service/asset.service';
@Component({
  selector: 'app-data-employee-popup',
  templateUrl: './data-employee-popup.component.html',
  styleUrls: ['./data-employee-popup.component.css']
})
export class DataEmployeePopupComponent implements OnInit {
  displayedColumns: string[] = ['no', 'assetCode', 'serialNumber', 'action'];
  public selectAsset: Array<any> = [];
  public otherPrefix: boolean;
  public test: [];
  xmap = new Map();

  public dateAsset: FormGroup;
  public items: FormArray;


  //cheackDup
  public table_employee: any;
  submitted: boolean = true;
  isError: boolean;
  duplicate: boolean;

  //get list data option
  public siteEm: any;

  public positionEm: any;
  public status: any;
  public emType: any;
  public putassetStatus: FormGroup;
  public wherestatus: FormGroup;
  public history: FormGroup;
  public formEmployee: FormGroup;
  public formEmpAsset: FormGroup;
  constructor(
    private router: Router,
    private assetService: AssetService,
    private historyassetService: HistoryassetService,
    private employeeAssetService: EmployeeAssetService,
    private employeeService: EmployeeService,
    private siteService: SiteService,
    private employeeTypeService: EmployeeTypeService,
    private positionService: PositionService,
    private formBuilder: FormBuilder,
    private statusService: StatusService,
    public dialog: MatDialog,
    public dialogRef: MatDialogRef<DataEmployeePopupComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
  ) { }
  createdForm() {
    this.formEmployee = this.formBuilder.group({
      employeeCode: new FormControl({ value: null, disabled: false }, [Validators.required]),
      nickname: new FormControl({ value: '', disabled: false }),
      prefix: new FormControl({ value: '', disabled: false }, [Validators.required]),
      firstname: new FormControl({ value: '', disabled: false }, [Validators.required]),
      lastname: new FormControl({ value: '', disabled: false }, [Validators.required]),
      type: new FormControl({ value: '', disabled: false }, [Validators.required]),
      position: new FormControl({ value: '', disabled: false }, [Validators.required]),
      site: new FormControl({ value: '', disabled: false }, [Validators.required]),
      workStart: new FormControl({ value: '', disabled: false }),
      workEnd: new FormControl({ value: '', disabled: false })
    });
    this.formEmpAsset = this.formBuilder.group({
      employeeId: new FormControl({ value: '', disabled: false }),
      assetId: new FormControl({ value: '', disabled: false }),
      receivedDt: new FormControl({ value: '', disabled: false }, [Validators.required]),
      status: new FormControl({ value: '', disabled: false }),
    });
    this.history = this.formBuilder.group({
      employeeCode: new FormControl({ value: '', disabled: false }, [Validators.required]),
      assetCode: new FormControl({ value: '', disabled: false }, [Validators.required]),

    });

    this.putassetStatus = this.formBuilder.group({
      _id: new FormControl({ value: '', disabled: false }, [Validators.required]),
      state: new FormControl({ value: '', disabled: false }, [Validators.required]),
    });
  }

  ngOnInit() {
    this.table_employee = this.data.dataKey
    console.log(this.table_employee)
    this.createdForm()
    //get list data option
    this.siteService.getSite().then(result => {
      this.siteEm = result.responseData.data;
    });
    this.positionService.getPosition().then(result => {
      this.positionEm = result.responseData.data;
    });
    this.employeeTypeService.getEmployType().then(result => {
      this.emType = result.responseData.data;
    });
    this.selectAsset
    console.log(this.selectAsset)
    this.statusService.getStatusUsed().then(result => {
      this.status = result[0]._id;
      this.putassetStatus.patchValue({
        state: this.status,
      });
    })


  }
  onChange(value) {
    if (value === "อื่นๆ") {
      this.formEmployee.patchValue({ prefix: '' })
      this.otherPrefix = true;
    }
    else {
      this.otherPrefix = false;
    }
  }
  deleteSelect(i) {
    console.log(i)
    this.selectAsset.splice(i, 1);
  }

  onSubmit() {
    try {
      this.checkDuplicate()
      if (this.duplicate === true || this.formEmployee.invalid)  {
        throw 'error'
      }
      else {
         //Check field date
        for (let receivedDt of this.selectAsset) {
          if(this.xmap.get(receivedDt._id) == null){
            throw 'error'
          }
        }
         //Add employee
        this.employeeService.getAddemployee(this.formEmployee.value)
        //add employee asset
        for (let item of this.selectAsset) {
          this.formEmpAsset.patchValue({
            employeeId: this.formEmployee.value.employeeCode,
            assetId: item._id,
            receivedDt: this.xmap.get(item.assetCode),
            status:'Y'
          })
          console.log(this.formEmpAsset)
          this.employeeAssetService.postEmpAsset(this.formEmpAsset.value)
        }
        if (this.historyasset() === true) {
          if (this.statusAssetFunc() === true) {
          }
        }
        Swal.fire({
          position: 'center',
          icon: 'success',
          title: 'Your work has been saved',
          showConfirmButton: false,
          timer: 1000
        })
        this.dialogRef.close();
      }
    } catch (error) {
        this.submitted = false
        this.isError = true;
        Swal.fire({
          icon: 'error',
          text: 'ข้อมูลไม่ถูกต้อง กรุณากรอกอีกครั้ง!',
        })
    }

  }

  openDialog() {
    this.selectAsset
    const dialogRef = this.dialog.open(PopupAssetComponent, {
      height: '80%',
      width: '70%',
    });
    dialogRef.afterClosed().subscribe(result => {
      console.log(result)
      for (let i = 0; i < result.length; i++) {
        this.selectAsset.push(result[i])
      }
    });
  }

  historyasset() {
    for (let item of this.selectAsset) {
      this.history.patchValue({
        employeeCode: this.formEmployee.value.employeeCode,
        assetCode: item._id
      })
      console.log(this.formEmpAsset.value)
      this.historyassetService.postHistoryasset(this.history.value).then(result => {
      })
    }
    return true;
  }

  statusAssetFunc() {
    for (let item of this.selectAsset) {
      this.putassetStatus.patchValue({
        _id: item._id,
        state: this.putassetStatus.value.state,
      })
      this.assetService.putAsset(this.putassetStatus.value).then((result) => {
      });
    }
    return true;
  }
  checkDuplicate() {
    for (let i = 0; i < this.table_employee.length; i++) {
      if (this.formEmployee.value.employeeCode == this.table_employee[i].employeeCode) {
        console.log("Duplicate");
        this.duplicate = true;
        i = this.table_employee.length;
      } else {
        this.duplicate = false;
      }
    }
  }
  onclickDate(indexSelect) {
    this.xmap.set(indexSelect, this.formEmpAsset.value.receivedDt)
    console.log(this.xmap)
    console.log(this.xmap.get(indexSelect))
  }

}
