import { Injectable } from '@angular/core';
import { HttpParams, HttpClient } from '@angular/common/http';
import { API } from '../url-constants';
import { HttpServices } from './http.service';

@Injectable({
  providedIn: 'root'
})
export class BrandService {

  constructor(
    public http: HttpServices
  ) { }
  getBrand() {
    const httpParams = new HttpParams();
    const requestOptions = {
      params: httpParams
    };
    return this.http.get(API.BRAND, requestOptions);
}
getListBrand() {
  const httpParams = new HttpParams();
  const requestOptions = {
    params: httpParams
  };
  return this.http.get(API.LISTBRAND, requestOptions);
}

postBrand(data: any){
  const httpParams = new HttpParams();
  const requestOptions = {
    params: httpParams
  };
  return this.http.post(API.CREATEBRAND, data);
}
deleteBrand(data: any) {
  console.log(data)
  const httpParams = new HttpParams();
  const requestOptions = {
    params: httpParams
  };
  return this.http.delete(API.DELETEBRAND.replace(':_id', data._id));
}
putBrand(data: any) {
  const httpParams = new HttpParams();
  const requestOptions = {
    params: httpParams
  };
  return this.http.put(API.PUTBRAND.replace(':_id', data._id), data);
}
}
