import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { assetModel } from 'src/app/model/assetModel.model';
import { AssetModelService } from 'src/app/service/assetModel.service';
import Swal from 'sweetalert2';
@Component({
  selector: 'app-add-model',
  templateUrl: './add-model.component.html',
  styleUrls: ['./add-model.component.css']
})
export class AddModelComponent implements OnInit {
  submitted: boolean
  AssetModelTable: any;
  duplicate: boolean;
  createdFormModel: FormGroup;

  constructor(
    private formBuilder: FormBuilder,
    public assetService: AssetModelService,
    public dialogRef: MatDialogRef<AddModelComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
  ) {}
  createdForm() {
    this.createdFormModel = this.formBuilder.group({
      modelType: new FormControl({ value: '', disabled: false }, [Validators.required]),
    });
  }

  ngOnInit() {
    this.createdForm()
    this.AssetModelTable = this.data.dataKey;
  }
  checkDuplicate() {
    for(let i = 0; i < this.AssetModelTable.length; i++){
      if(this.createdFormModel.value.modelType == this.AssetModelTable[i].modelType){
        this.duplicate = true;
        i =this.AssetModelTable.length;
      } else {
        this.duplicate = false;
      }
    }
  }
  onSubmit(){
    this.checkDuplicate();
    if (this.duplicate == true || this.createdFormModel.value.modelType === "" ) {
      this.submitted = false
    } else {
      this.submitted = true
    this.assetService.postAssetModel(this.createdFormModel.value).then(() => {
     });
     Swal.fire({
      position: 'center',
      icon: 'success',
      title: 'Your work has been saved',
      showConfirmButton: false,
      timer: 1000
    })
    this.dialogRef.close();
  }
}
onCancel(){
  this.dialogRef.close();
}
}
