import { SelectionModel } from '@angular/cdk/collections';
import { Component, Inject, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormControl, FormGroup } from '@angular/forms';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { AssetService } from 'src/app/service/asset.service';
import { AssetModelService } from 'src/app/service/assetModel.service';
import { BrandService } from 'src/app/service/brand.service';
import { TypeService } from 'src/app/service/type.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-popup-asset',
  templateUrl: './popup-asset.component.html',
  styleUrls: ['./popup-asset.component.css']
})
export class PopupAssetComponent implements OnInit {
  public dataType: any;
  public dataBrand: any;
  public dataModel : any;
  public dataStatus : any;
  listData: MatTableDataSource<any>;
  displayedColumns: string[] = ['select','no','assetCode','type','brand','model','color','serialNumber','remark','status'];
  dataSource: MatTableDataSource<any>;
  selection = new SelectionModel(true, []);

  public dataAsset : any;
  public search :FormGroup;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private formBuilder: FormBuilder,
    private brandService: BrandService,
    private typeService: TypeService,
    private assetModelService: AssetModelService,
    public dialogRef: MatDialogRef<PopupAssetComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public dialog : MatDialog,
    private assetService: AssetService,
  ) {}

  selectedRowIndex = -1;
  highlight(row: { id: number; }){
    this.selectedRowIndex = row.id;
}

  ngOnInit(){
    this.search = this.formBuilder.group({
      searchText: new FormControl({ value:'', disabled: false }),
    });

    this.brandService.getBrand().then(result => {
      this.dataBrand = result.responseData.data;
    });
    this.typeService.getType().then(result => {
      this.dataType = result.responseData.data;
    });
    this.assetModelService.getAssetModel().then(result => {
      this.dataModel = result.responseData.data;
    });
    this.assetService.getlistassetFree().then(result => {
      this.dataAsset = result;
      this.dataSource = new MatTableDataSource(this.dataAsset);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    })
  }
  public doFilter = (value: string) => {
    this.dataSource.filter = value.trim().toLocaleLowerCase();
  }
  public typeFilter = (value: string) => {
    this.dataSource.filter = value.trim().toLocaleLowerCase();
  }
  public brandFilter = (value: string) => {
    this.dataSource.filter = value.trim().toLocaleLowerCase();
  }
  public modelFilter = (value: string) => {
    this.dataSource.filter = value.trim().toLocaleLowerCase();
  }

  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.dataSource.data.length;
    return numSelected === numRows;
  }

  masterToggle() {
    this.isAllSelected() ?
        this.selection.clear() :
         this.dataSource.data.forEach(row => this.selection.select(row));
  }
  onSelect() {
    if(this.selection.selected.length == 0){
      Swal.fire({
        icon: 'error',
        text: 'กรุณาเลือกทรัพย์สิน',
      })
    }else{
      this.dialogRef.close(this.selection.selected)
    }
  }
  onCancel(){
    this.dialogRef.close();
  }
  clearFilters(){
    this.assetService.getlistassetFree().then(result => {
      this.dataAsset = result;
      this.dataSource = new MatTableDataSource(this.dataAsset);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    })
    this.search.patchValue({
      searchText: null
    })
    }
}



