import { Component, OnInit, ViewChild } from '@angular/core';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Router } from '@angular/router';
import { BrandService } from 'src/app/service/brand.service';
import { AddBrandComponent } from '../add-brand/add-brand.component';
import { EditBrandComponent } from '../edit-brand/edit-brand.component';
import Swal from 'sweetalert2';
interface manage {
  value: string;
  viewValue: string;
}
@Component({
  selector: 'app-brandnormal',
  templateUrl: './brandnormal.component.html',
  styleUrls: ['./brandnormal.component.css']
})

export class BrandnormalComponent implements OnInit {

  listData: MatTableDataSource<any>;
  displayedColumns: string[] = ['no','brandName','typeName', 'updateDt', 'updateBy', 'action'];
  dataSource: MatTableDataSource<any>;
  public dataBrand: any;
  public dataType: any;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private router: Router,
    public dialog: MatDialog,
    private brandService: BrandService,
  ) { }

  selectedRowIndex = -1;
  highlight(row: { id: number; }) {
    this.selectedRowIndex = row.id;
  }

  ngOnInit() {
    this.brandService.getListBrand().then(result => {
      this.dataBrand = result
      this.dataSource = new MatTableDataSource(this.dataBrand);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    })
  }
  manages: manage[] = [
    {value: '1', viewValue: 'Position'},
    {value: '2', viewValue: 'Site'},
    {value: '3', viewValue: 'Brand'},
    {value: '4', viewValue: 'CPU'},
    {value: '5', viewValue: 'Asset Type'},
    {value: '6', viewValue: 'Employee Type'},
    {value: '7', viewValue: 'Status'},
  ];

  public doFilter = (value: string) => {
    this.dataSource.filter = value.trim().toLocaleLowerCase();
  }

  clickdelete(data) {
    Swal.fire({
      title: 'Delete Brand',
      text: "Are you sure you want to delete this Brand?",
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: 'rgb(70, 0, 0)',
      cancelButtonColor: '#696969',
    }).then((result) => {
      if (result.isConfirmed) {
        this.brandService.deleteBrand(data).then(() => {
        });
        this.refresh()
        Swal.fire(
          'Deleted!',
          'Your file has been deleted.',
          'success'
        )
      }
    })
  }
  openDialog() {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.data = this.dataBrand;
    this.dialog.open(AddBrandComponent, {
      data: {
        dataKey: this.dataBrand
      }
    }).afterClosed().subscribe(result => {
      this.refresh();
    });
  }
  openDialogEditbrand(data) {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.data = data;
    console.log(data)
    this.dialog.open(EditBrandComponent, {
      data: {
        dataKey: data,
        dataTable: this.dataBrand,
        dataKeyType: this.dataType,
      }
    }).afterClosed().subscribe(result => {
      this.refresh();
    });
  }
  refresh() {
    this.brandService.getListBrand().then(result => {
      this.dataBrand = result
      this.dataSource = new MatTableDataSource(this.dataBrand);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    })
  }
  onChange(value){
    switch(value){
      case '1' : this.router.navigate(['management',{}]); break;
      case '2' : this.router.navigate(['site',{}]); break;
      case '3' : this.router.navigate(['brand',{}]); break;
      case '4' : this.router.navigate(['model',{}]); break;
      case '5' : this.router.navigate(['type',{}]); break;
      case '6' : this.router.navigate(['emtype',{}]); break;
      case '7' : this.router.navigate(['status',{}]); break;
    }
  }
  onclickListManage(){
    this.router.navigate(['listManage', {}])
  }
  onclickEmployee() {
    this.router.navigate(['employee',{}])
  }
  onclickAsset() {
    this.router.navigate(['data-asset',{}])
  }
  onclickdataadmin() {
    this.router.navigate(['admin',{}])
  }
  onclickchangead() {
    this.router.navigate(['changead',{}])
  }
  onclickaccount() {
    this.router.navigate(['list-account',{}])
  }
  logout() {
    localStorage.removeItem('Authorization');
    this.router.navigate(['login', {}])
  }
}

