import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { EmployeeTypeService } from 'src/app/service/employee-type.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-emtype-edit',
  templateUrl: './emtype-edit.component.html',
  styleUrls: ['./emtype-edit.component.css']
})
export class EmtypeEditComponent implements OnInit {
  submitted : boolean
  public typeTable: any;
  public emType: string;
  duplicate: boolean;
  type_chackDup: any;
  oldData:any;
  createdFormEmtypt: FormGroup;
  constructor(
    private formBuilder: FormBuilder,
    private employeeTypeService: EmployeeTypeService,
    public dialogRef: MatDialogRef<EmtypeEditComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
  ) { }
  ngOnInit() {
    this.typeTable = this.data.dataKey;
    this.emType = this.typeTable.emType
    this.type_chackDup = this.data.dataTable
    this.createdFormEmtypt = this.formBuilder.group({
      emType: new FormControl({ value: '', disabled: false }, [Validators.required]),
  });
  this.createdFormEmtypt.patchValue({
    emType:this.emType,
  })
  this.oldData=this.emType
  }
  checkDuplicate() {
    for(let i = 0; i < this.type_chackDup.length; i++){
      if(this.createdFormEmtypt.value.emType == this.type_chackDup[i].emType){
        console.log("Duplicate");
        this.duplicate = true;
        i =this.type_chackDup.length;
      } else {
        this.duplicate = false;
      }
    }
  }
  onCancel(){
    this.dialogRef.close();
  }

  onSubmit() {
    if(this.oldData == this.createdFormEmtypt.value.emType){
      this.dialogRef.close();
    }else{
        this.checkDuplicate();
        console.log(this.emType)
        if (this.duplicate == true || this.createdFormEmtypt.value.emType === '' ) {
          this.submitted = false
        } else {
          this.submitted = true
          this.typeTable.emType = this.createdFormEmtypt.value.emType;
          this.employeeTypeService.putTypeEm(this.typeTable).then(() => {
        });
        Swal.fire({
          position: 'center',
          icon: 'success',
          title: 'Your work has been saved',
          showConfirmButton: false,
          timer: 1000
        })
        this.dialogRef.close();
        }
    }

}
}
